<template>
	<div class="my-echarts">
		<div class="ui-content-box">
			<div class="e-nav-top">
			
			<div class="fr nav-right">
				   <div class="block fl">
					    <el-date-picker
					      v-model="timedata"
					      type="daterange"
					      align="right"
					      placeholder=timedata
					      :picker-options="pickerOptions2"
					      @change="timeChange">
					    </el-date-picker>
					  </div>
				 <div class="nav-btn bg-blue" v-on:click='timeChange()'>刷新数据</div><div class="nav-btn bg-grav"><router-link to="/statistics">返回</router-link></div>				 
			</div>			
		</div>
		<div class="e-content">
			<div class="e-content-m">
				<div class="e-width-3">
					<div id="pieEchart" style="width: 100%;height:400px;"></div>
				</div>
				<div class="e-width-3">
					<div class="e-title">案例浏览TOP10 <button>导出</button></div>
					<ul class="ui-list">
						<li>
							<span class="w-70 col-block">案例名称</span>
							<span class="w-30 col-block">浏览量</span>
						</li>
						<li v-for="el in scanCaseDatas">
							<span class="w-70">{{el.caseTitle}}</span>
							<span class="w-30">{{el.visited.length}}</span>
						</li>
						
					</ul>
				</div>
				<div class="e-width-3">
					<div class="e-title">案例收藏TOP10 <button>导出</button></div>
					<ul class="ui-list">
						<li>
							<span class="w-70 col-block">案例名称</span>
							<span class="w-30 col-block">收藏量</span>
						</li>
						<li v-for="item in collectCaseDatas">
							<span class="w-70">{{item.caseTitle}}</span>
							<span class="w-30">{{item.liked.length}}</span>
						</li>
						
					</ul>
				</div>				
			</div>
			<button class="btn-export" style="left: 40%;">导出</button>
			<div id="passEchart" class="fl" style="width: 50%;height:400px;"></div>
			<button class="btn-export" style="left: 90%;">导出</button>
			<div id="putEchart" class="fl" style="width: 50%;height:400px;"></div>
		</div>
		</div>
		
	</div>
	
</template>
<script type="text/javascript">
	import Vue from 'vue'
	import $ from 'jquery'
	import echarts from 'echarts'
	
	export default {
      name: 'MyCase',
       data () {
        return {
        	domain:'http://qdzy.internal-i-focusing.com',
	        pickerOptions2: {	        	
	          shortcuts: [{
	            text: '最近一个月',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近三个月',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近半年',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近一年',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
	              picker.$emit('pick', [start, end]);
	            }
	          }]
	        },
	        timedata: [new Date().getTime() - 3600 * 1000 * 24 * 30,new Date().getTime()],	        
	        
	        //案例浏览
	        scanCaseDatas:[],
	        //案例收藏
	        collectCaseDatas:[],
	        
      	     //图形初始化
      	     initPieEchart:'',
			  initPassEchart:'',
			 initPutEchart:'',
			 getPie:'',
			 getPass:'',
			 getPut:'',
			 getScan:'',//浏览量top
			 getCollect:'',//收藏量top
			 userType:''
        }
      },  
      
      mounted:function(){   
      	var _this=this;      	
		_this.userType=_this.$parent.userType
		//饼状图
		_this.initPieEchart=function (datas){
			var pieEchart = echarts.init(document.getElementById('pieEchart'));
			
				pieEchart.setOption({
					tooltip: {
			        trigger: 'item',
			        formatter: "{a} <br/>{b}: {d}%"
			    },
			    legend: {
			        orient: 'vertical',
			        x: 'left',
			        data:['审核通过','未审核案例','审核驳回']
			    },
			    color:['#60a7e7','#57c5d2','#fb7aa2'],
			    series: [
			        {
						name:'访问来源',
			            type:'pie',
			            radius: ['40%', '55%'],
			            center: ['50%', '50%'],
			
			            data:datas
			        }
			    ]
			})
		};
		
		//各省审核通过率TOP10
		_this.initPassEchart=function(xdata,ydata){			
	      	var passEchart = echarts.init(document.getElementById('passEchart'));
	      	passEchart.setOption({
		    title: { 
		    	text: _this.userType=='provinceUser'?'各地市审核通过率TOP10':'各省审核通过率TOP10' 
		    },
		    tooltip: {
		    	trigger: 'item',  
        		formatter: '{b}:\n{c}%' 
		    },
		    xAxis: {
		        data: xdata
		    },
		    yAxis: [  
		        {  
		            type: 'value',  
		            axisLabel: {  
		                  show: true,  
		                  interval: 'auto',  
		                  formatter: '{value}%'  
		                },  
		            show: true  
		        }  
		    ], 
		    series: [{
		        name: '积分',
		        type: 'bar',
		        data: ydata,
		        barWidth:20,
		        
		        itemStyle:{
		        	normal:{
		        		color:'#60a7e7',
		        		label: {
                            show: true,
                            position: 'top',
                            formatter: '{c}%' 
                        }
		        	},
		        	
		        }
		    }]
			})
		};
		
		//各省提交量TOP10		
		_this.initPutEchart=function (xdata,ydata){
			var putEchart = echarts.init(document.getElementById('putEchart'));
		      	putEchart.setOption({
			    title: { 
			    	text: _this.userType=='provinceUser'?'各地市提交量TOP10':'各省提交量TOP10' 
			    },
			    tooltip: {
			    	trigger: 'item',  
        			formatter: '{b}:\n{c}' 
			    },
			    xAxis: {
			        data: xdata
			    },
			    yAxis:{  
		            type: 'value',  
		            axisLabel: {  
		                  show: true,  
		                  interval: 'auto',  
		                  formatter: '{value}'  
		                },  
		            show: true  
		        }  ,
		       
			    series: [{
			        name: '积分',
			        type: 'bar',
			        data: ydata,
			        barWidth:20,			        
			        itemStyle:{
			        	normal:{			        		
			        		color:'#60a7e7',
			        		label: {
	                            show: true,
	                            position: 'top',
	                            formatter: '{c}' 
	                        }
			        	}			        	
			        }
			    }
			    
			    ]
				})
		};


		//饼状图
		_this.getPie=function(timeData){	
      		var _url=_this.domain+'/cusWifs/statistics/getPiChart';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				var rutData=[
      						{value:res.ratio.approved, name:'审核通过'},
			                {value:res.ratio.notApproved, name:'未审核案例'},
			                {value:res.ratio.reject, name:'审核驳回'}      				
      				]
      				_this.initPieEchart(rutData)      				
      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});      		
      	};
      	//省审核通过
      	_this.getPass=function(timeData){
      		var _url=_this.domain+'/cusWifs/statistics/getProvincePassReportByProvince';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){      				
      				var xdata=[];
      				var ydata=[];
      				for(var x in res.statistics){
      					xdata.push(res.statistics[x].province)
      					ydata.push(parseFloat(res.statistics[x].publishcount/res.statistics[x].allcount*100).toFixed(2))
      					
      				}
      				_this.initPassEchart(xdata,ydata);    				

      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});       		
      	};
      	//省提交量
      	_this.getPut=function(timeData){
      		var _url=_this.domain+'/cusWifs/statistics/getProvinceSubmitReportByProvince';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				var xdata=[];
      				var ydata=[];
      				for(var x in res.statistics){
      					xdata.push(res.statistics[x].province);      				
      					ydata.push(res.statistics[x].submitcount);
      				}
      				_this.initPutEchart(xdata,ydata);
      				
      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});       		
      	};
      	//浏览top
      	_this.getScan=function(timeData){
      		var _url=_this.domain+'/cusWifs/statistics/visitedTop10';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				if(res.error==false){
      					_this.scanCaseDatas=res.list
      				}
      				
      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});  
      		
      	};
      	//收藏量top
		 _this.getCollect=function(timeData){
      		var _url=_this.domain+'/cusWifs/statistics/likeTop10';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				_this.collectCaseDatas=res.list
      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});  
		 };   
		 //第一次加载数据
		 function initDatas(){
		 	var start=new Date().getTime() - 3600 * 1000 * 24 * 7;
		 	var end=new Date().getTime();	
      		_this.timeData=[start,end];
      		
      		var time={
      			startTime:start,
      			endTime:end
      		}; 
      		$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.userType=res.content.userLevel;
						_this.getPie(time);   
			      		_this.getPass(time);
			      		_this.getPut(time);
			      		_this.getScan(time);//浏览量top
						_this.getCollect(time);//收藏量top						
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})		 	
		 };
		 initDatas();
      },
      methods:{
      	timeChange:function(){
      		var _this=this;
      		var timeData={
      			startTime:_this.timedata[0].getTime(),
      			endTime:_this.timedata[1].getTime()
      		};
      		_this.getPie(timeData);   
      		_this.getPass(timeData);
      		_this.getPut(timeData);
      		_this.getScan(timeData);//浏览量top
			_this.getCollect(timeData);//收藏量top
      	},     	
      },
         
     }
</script>
<style scoped="scoped">
	.my-echarts{
		padding: 20px;
		
	}
	.e-nav-top{
		overflow: hidden;
		padding:20px 1% 0px;
	}
	.col-grav{
		color: #CCCCCC;
	}
	.nav-btn{
		width: 120px;
		height: 42px;
		line-height: 42px;
		text-align: center;
		border: none;
		margin-left:20px;
		outline: none;
		cursor: pointer;
		font-size: 16px;	
		color: #FFFFFF;	
		border-radius: 5px;
		float: left;
		display: inline-block;
	}
	.bg-blue{
		background: #45A8DC;
		
	}
	.bg-grav{
		background: #D2D5D7;
	}
	.e-content{
		margin-top: 30px;
		position: relative;
		overflow: hidden;
		
	}
	.btn-export{
		width: 60px;
		height: 25px;
		border: 1px solid #E5E5E5;
		border-radius: 5px;
		color: #999999;
		font-size: 12px;
		background: #FFFFFF;
		outline: none;
		cursor: pointer;
		position: absolute;
		/*right: 20px;*/
				
	}

	.e-content-m{
		overflow: hidden;
		min-width: 1070px;
		margin-bottom: 40px;
	}
	.e-content-m .e-width-3{
		width: 28%;
		min-width:300px;
		background: #FFFFFF;
		float: left;
		margin: 1%;
		border-radius: 10px;	
		padding: 1.5%;	
		height: 420px;
		box-shadow: 2px 2px 25px 1px rgba(0,0,0,.1);		
	}
	.e-content-m .e-width-3:last-child{
		margin-right: 0;
	}
	.e-title{
		font-size: 20px;
		text-align: center;
		color: #60a7e7;
		position: relative;
	}
	.e-title button{
		width: 60px;
		height: 25px;
		border: 1px solid #E5E5E5;
		border-radius: 5px;
		color: #999999;
		font-size: 12px;
		background: #FFFFFF;
		outline: none;
		cursor: pointer;	
		position: absolute;
		right: 0;
	}
	.ui-list{
		width: 100%;			
		font-size: 14px;
		color: #BEBEBE;
	}
	.col-block{
		color: #333;
	}
	.ui-list li{
		overflow: hidden;
		height:35px;
		line-height: 35px;	
	}
	.ui-list li:first-child{
		height: 50px;
		line-height: 50px;
	}
	.ui-list .w-70{
		display: inline-block;
		width: 69%;
		text-align: left;	
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;	
	}
	.ui-list .w-30{
		display: inline-block;
		width:60px;
		text-align: center;
		float: right;
	}
	.nav-btn a{
		color: #FFFFFF!important;	
		text-decoration: none;
	}
	.nav-right{
		margin-right:1% ;
	}
	.el-date-editor--daterange.el-input{
		width: 270px;
	}
</style>


