<template>
	<div class="pointTable ui-content">
		<div class="ui-content-box">
			<div style="overflow: hidden;">
				<div class="fl">
				<!--<el-checkbox v-model="checked" style="font-size: 18px;" @change="test()">全选</el-checkbox>-->
				<el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
				<el-select v-model="value8" filterable  @change="slectChange(value8)" class="selectW">
			    <el-option
			      class="selectP"
			      v-for="item in options"
			      :label="item.label"
			      :value="item.value">
			    </el-option>
		  	</el-select>		  		
				<el-button type="primary" class="Sta" @click="InvitedUsers">邀请用户</el-button>
				<el-button type="primary" class="Sta" @click="frozenAccounts">冻结帐号</el-button>
				<el-button type="primary" class="Sta" @click="activateAccounts">激活帐号</el-button>
			</div>			
		<div class="fr ui-search">
	        <i class="icon-search"></i>
	        <input type="text" placeholder="请输入姓名" class="searchName">
	        <button @click="searchName">搜索</button>
      </div>
		</div>		
		<div class="taBle">				
			<ul>
				<li>手机号</li>
				<li>用户名</li>
				<li>操作</li>
			</ul>	
			<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">  
			<table border="1">
			  <tbody>			  	 				 		  
   				  <tr v-for="(item,index) in IntegralDetailed">
				      <td>
				      	<!--<el-checkbox v-model="item.checked" @change="rest2()">{{item.userId}}</el-checkbox></td>-->
				      	<el-checkbox :label="item._id">{{item.phone}}</el-checkbox>   		
				      <td>
				      	{{item.name}}
				      </td>
				      <td>
				      	<el-button type="primary"  class="Invited"  v-if="item.isInvited == true" @click="endInvited(item._id)">已邀请</el-button>
				      	<el-button type="primary"  class="userButton"  v-else-if="item.isInvited == false" @click="invitedUser(item._id,item.bound)">邀请用户</el-button>
				      	<el-button type="primary"  class="userButton"  v-if="item.isFreeze == false" @click="frozenAccount(item._id)">冻结帐号</el-button>
				      	<el-button type="primary"  class="Active" v-else-if="item.isFreeze == true" @click="activateAccount(item._id)">激活帐号</el-button>
				      	<el-button type="primary" v-show="userLevel=='superAdmin'"  class="userButton"  @click="deletAccount(item._id)">删除用户</el-button>
				      	<el-select v-show="userLevel=='groupUser'||userLevel=='superAdmin'"  v-model="item.userLevel" size="small" filterable placeholder="请选择" class="checKedD" @change="optChange(item)">
							    <el-option
							      v-for="item in optionss"
							      :label="item.label"
							      :value="item.value">
							    </el-option>
	  					</el-select>
				      </td>
			    </tr>  	
			  </tbody>
			</table>
			</el-checkbox-group>	
		</div>	
		  <div class="block" v-show="amount>10">
		    <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :page-size="10"
		      layout="prev, pager, next"
		      :total="amount">
		    </el-pagination>
		  </div>
		  <div class="btn-add">
		  	<el-button type="primary" class="Sta fl" @click="creatUsers">创建新用户</el-button>
		  </div>
		</div>
	<popbox></popbox>
	<creatuser></creatuser>
	<success></success>
	</div>
</template>
<script>
  import $ from 'jquery';
  import popbox from '../pop-up-box/pop-up';
  import creatuser from '../pop-up-box/creatuser';
  import success from '../pop-up-box/succeed';
  export default {
  	components:{
  		popbox,
  		creatuser,
  		success
  	},
  	created:function(){
  		 		
  	},
  	mounted:function(){
  		var _this = this;
  		//
  		    $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	if(!ress.error){
    					  		console.log(ress.content.userLevel);
    					  	   _this.userLevel=ress.content.userLevel;
    					  	   _this.adress = ress.content.proAddress;
    					  	   if(ress.content.userLevel=='provinceUser'){
    					  	   	_this.options=[
		    					  	   		{
		    					  	   			lable:'全省',
		    					  	   			value:'全省'
		    					  	   		}
    					  	          	];
    					  	    _this.value8='全省';  	
    					  	   		$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{name:ress.content.proAddress}
											}),
											success:function(res){
							 						if(!res.error){
												 	for(var a = 0;a<res.list[0].cities.length;a++){
						 							var  mm = {
						 										value:res.list[0].cities[a],
						 										label:res.list[0].cities[a]
						 								};
						 							_this.options.push(mm);
						 						  };
						 					
												}
											}
									});
    					  	   }else{
    					  	   			_this.options=[
		    					  	   		{
		    					  	   			lable:'全国',
		    					  	   			value:'全国'
		    					  	   		}
    					  	          	];
    					  	    _this.value8='全国';  	
    					  	   		$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{}
											}),
											success:function(res){
							 						if(!res.error){
												 	for(var a = 0;a<res.list.length;a++){
 					
							 							var  mm = {
							 										value:res.list[a].name,
							 										label:res.list[a].name
							 									};
							 							_this.options.push(mm);
							 						};
						 					
												}
											}
									});
    					  	   }	  	     					  	   
    					  	} 					  	  			   
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    			});		
  		//每页显示
  		$.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
    			pageNumber: '',   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			conditions:{
    				userLevel:{$ne:'superAdmin'}
    			},
    			sort: {createTime: -1},
    			}),
             success:function(res){
             		console.log(res);
             		_this.IntegralDetailed = res.list;
             		_this.amount = res.count;
             		console.log(_this.IntegralDetailed);
             		for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);				
  		}
            }
  		});
  		
  		console.log(this.cityOptions);
  	},
    methods: {
    	   	deletAccount:function(val){
    		  	 var _this = this;
      	    $.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/delete",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				id:val,
    			}),
             success:function(res){
    			if(!res.error){
    				  for(var a = 0;a<_this.IntegralDetailed.length;a++){
    				  	  if(_this.IntegralDetailed[a]._id == val){
    				  	  	  _this.IntegralDetailed.splice(a,1);
    				  	  }
    				  }
    			}
            }
  		});
    	},
    	optChange:function(item){
    		console.log(item.userLevel);
    		$.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/modify",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				id:item._id,         //指明需要修改的实体ID
				content: {userLevel:item.userLevel}        //指明需要修改的实体内容的字段及值
    			}),
             success:function(res){
             		console.log(res);
           			 }
  				});   		
    		},
   
      //搜索     
      searchName:function(){
      	 var _this = this;
      	 
      	    $.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
    			pageNumber: '',   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			conditions:{
    				userLevel:{$ne:'superAdmin'},
    				name:$('.searchName').val()==''?undefined:{$regex:$('.searchName').val()},
    				proAddress:_this.slectdisc=='全国'?undefined:{$regex:_this.slectdisc}
    				},
    			sort: {createTime: -1},
    			}),
             success:function(res){
             		console.log(res);
             		_this.IntegralDetailed = res.list;
             		_this.amount = res.count;
             		console.log(_this.IntegralDetailed);
             		for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);				
  		}
            }
  		});
      },
      handleSizeChange(val) {
        //console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
      	var _this = this;
        this.currentPage = val;
        console.log(`当前页: ${val}`);
        $.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
    			pageNumber: val-1,   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			conditions:{
    				userLevel:{$ne:'superAdmin'}
    			},
    			sort: {},
    			}),
             success:function(res){
             		console.log(res);
             		_this.IntegralDetailed = res.list;
             		console.log(_this.IntegralDetailed);
             		for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);				
  		}
            }
  		}); 		
      },
      //多选邀请用户
      InvitedUsers:function(){
      	 	//document.getElementsByClassName('shade')[0].style.display='block';
      	 	var _this  = this;
      	 	$('.shade').fadeIn();
      	 	$('.shade .pop-is').text('是否邀请用户？');
      	 	$('.sure').click(function(){
      	 				$.ajax({
						url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
						method:'post',
						dataType:'json',
      	 				data:JSON.stringify({category:'Users',ids:_this.checkedCities,content:{isInvited:true}}),
      	 				success:function(res){
      	 							console.log(res);
      	 							if(!res.error){
      	 								$('.shade').fadeOut();
      	 								for(var m = 0;m<_this.checkedCities.length;m++){
      	 									console.log(_this.checkedCities[m]);
      	 								  for(var a = 0;a < _this.IntegralDetailed.length;a++){
      	 								  	console.log(_this.IntegralDetailed[a]._id)
      	 									if(_this.checkedCities[m]==_this.IntegralDetailed[a]._id){
      	 										_this.IntegralDetailed[a].isInvited = true;
      	 									}
      	 							      }   
      	 								} 
      	 							   $('.shade').fadeOut();
      	 							} 	 							
      	 				},
      	 				error:function(){
      	 							$('.shade').fadeOut();
      	 					  }
      	 			});
      	 	});
      	 	$('.callOff').click(function(){
      	 		$('.shade').fadeOut();
      	 	});
      },
      //创建用户
      creatUsers:function(){
      		//document.getElementsByClassName('userCreat')[0].style.display='block';
      		$('.userCreat').show();
      },
      //多选激活
      activateAccounts:function(){
      		var _this = this;
      	 	$('.shade').fadeIn();
      	 	$('.shade .pop-is').text('是否激活用户？');
      	 	$('.sure').click(function(){
      	 				$.ajax({
						url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
						method:'post',
						dataType:'json',
      	 				data:JSON.stringify({category:'Users',ids:_this.checkedCities,content:{isFreeze:false}}),
      	 				success:function(res){
      	 							console.log(res);
      	 							if(!res.error){
      	 								$('.shade').fadeOut();
      	 								for(var m = 0;m<_this.checkedCities.length;m++){
      	 									console.log(_this.checkedCities[m]);
      	 								  for(var a = 0;a < _this.IntegralDetailed.length;a++){
      	 								  	console.log(_this.IntegralDetailed[a]._id)
      	 									if(_this.checkedCities[m]==_this.IntegralDetailed[a]._id){
      	 										_this.IntegralDetailed[a].isFreeze = false;
      	 									}
      	 							      }   
      	 								}  
      	 							} 	 							
      	 				},
      	 				error:function(){
      	 							$('.shade').fadeOut();
      	 					  }
      	 			});
      	 	});
      	 	$('.callOff').click(function(){
      	 		$('.shade').fadeOut();
      	 	});
      },
      //多选冻结
      frozenAccounts:function(){
      	    var _this = this; 
      		$('.shade').fadeIn();
      	 	$('.shade .pop-is').text('是否冻结帐号？');
      	 	$('.sure').click(function(){
      	 				$.ajax({
						url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
						method:'post',
						dataType:'json',
      	 				data:JSON.stringify({category:'Users',ids:_this.checkedCities,content:{isFreeze:true}}),
      	 				success:function(res){
      	 							console.log(res);
      	 							if(!res.error){
      	 								$('.shade').fadeOut();
      	 								for(var m = 0;m<_this.checkedCities.length;m++){
      	 									console.log(_this.checkedCities[m]);
      	 								  for(var a = 0;a < _this.IntegralDetailed.length;a++){
      	 								  	console.log(_this.IntegralDetailed[a]._id)
      	 									if(_this.checkedCities[m]==_this.IntegralDetailed[a]._id){
      	 										_this.IntegralDetailed[a].isFreeze = true;
      	 									}
      	 							      }   
      	 								}     	 								
      	 							} 	 							
      	 				},
      	 				error:function(){
      	 							$('.shade').fadeOut();
      	 					  }
      	 			});
      	 	});
      	 		$('.callOff').click(function(){
      	 		$('.shade').fadeOut();
      	 	});
      },
      //单个冻结
      frozenAccount:function(num){
      	   	var that = this;
      	    $('.shade1').fadeIn();
      	    $('#successMessage').text('冻结成功！');
      	    $('.close').click(function(){
      	    	 $('.shade1').fadeOut();
      	    	 $('.Invited').unbind('click');
      	    	 for(var a = 0;a < that.IntegralDetailed.length;a++){
      	 				if(that.IntegralDetailed[a]._id == num){
      	 					that.IntegralDetailed[a].isFreeze = true;
      	 					$.ajax({
								url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
								method:'post',
								dataType:'json',
      	 						data:JSON.stringify({category:'Users',id:that.IntegralDetailed[a]._id,content:{isFreeze:true}}),
      	 						success:function(res){
      	 							console.log(res);
      	 						},
      	 						error:function(){
      	 							
      	 						}
      	 					})
      	 				};
      	   			};
      	    });
      },
      //单个激活
      activateAccount:function(num){
      	   	var that = this;
      	    $('.shade1').fadeIn();
      	    $('#successMessage').text('激活成功！');
      	    $('.close').click(function(){
      	    	 $('.shade1').fadeOut();
      	    	 $('.Invited').unbind('click');
      	    	 for(var a = 0;a < that.IntegralDetailed.length;a++){
      	 				if(that.IntegralDetailed[a]._id == num){
      	 					that.IntegralDetailed[a].isFreeze = false;
      	 					$.ajax({
								url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
								method:'post',
								dataType:'json',
      	 						data:JSON.stringify({category:'Users',id:that.IntegralDetailed[a]._id,content:{isFreeze:false}}),
      	 						success:function(res){
      	 							console.log(res);
      	 						},
      	 						error:function(){
      	 							
      	 						}
      	 					})
      	 				};
      	   			};
      	    });  
      },
      //单个邀请
 invitedUser:function(num,num2){
      	    var that = this;     	   
      	    $('.close').click(function(){
      	   		 $('.shade1').fadeOut();
      	    })
      	    $('.Invited').unbind('click');
      	    $('.shade').fadeIn();
      	 	$('.shade .pop-is').text('是否邀请用户？');
      	 	$('.sure').click(function(){
      	    	
      	    	 if(num2==false){
      	    	 	$('.shade1').fadeIn();
      	   			$('#successMessage').text('用户未关注！');
      	    	 }else{
      	    	 	  $.ajax({
      	    	 	  	url:'http://qdzy.internal-i-focusing.com/easyChat/sendP2pMessage',
						method:'post',
						dataType:'json',
      	 				data:JSON.stringify({
      	 						id:num
      	 					}),
      	 				success:function(res){
      	 					 if(!res.error){
      	 					 	 $.ajax({
									url:'http://qdzy.internal-i-focusing.com/wif/data/modify',
									method:'post',
									dataType:'json',
				      	 			data:JSON.stringify({category:'Users',id:num,content:{isInvited:true}}),
				      	 			success:function(ress){
				      	 						if(!ress.error){
				      	 								$('.shade').fadeOut();
				      	 								$('.shade1').fadeIn();
      	   									 			$('#successMessage').text('邀请成功！');
      	   									 			for(var a = 0;a < that.IntegralDetailed.length;a++){
      	   									 				if(that.IntegralDetailed[a]._id == num){
      	   									 					that.IntegralDetailed[a].isInvited=true
      	   									 			}
				      	 							}
      	   									 }			
				      	 				},
				      	 			error:function(){
				      	 							
				      	 					}    	 							
      	 					 });
      	 				 }
      	 			}
      	    	});	
      	    };
      	    });
      	$('.callOff').click(function(){
      	 		$('.shade').fadeOut();
      	 	});
      },
      endInvited:function(){
      	
      },
       handleCheckAllChange(event) {
        	this.checkedCities = event.target.checked ? this.cityOptions : [];
        	this.isIndeterminate = false;
        	console.log(this.checkedCities);
      },
      handleCheckedCitiesChange(value) {
        	let checkedCount = value.length;
        	this.checkAll = checkedCount === this.cityOptions.length;
       	    this.isIndeterminate = checkedCount > 0 && checkedCount < this.cityOptions.length;
            console.log(this.checkedCities);
      },
      //切换获取用户
      slectChange:function(val){
      	var _this = this;
      	$('.searchName').val('');
      	this.slectdisc = val;   	  	
      	if(val=='全国'&&_this.userLevel=='groupUser'){
      		$.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				conditions: {
					userLevel:{$ne:'superAdmin'}
				},     //查询条件，格式与mongodb查询条件相同
				pageNumber: 0,   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			}),
            success:function(res){
             		if(!res.error){
             			_this.amount = res.count;
             			_this.IntegralDetailed = res.list;
             		}
           		  }
  				}); 
      		}else if(_this.userLevel=='groupUser'){
      				$.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				conditions: {
					proAddress:val,
					userLevel:{$ne:'superAdmin'}
				},     //查询条件，格式与mongodb查询条件相同
				pageNumber: 0,   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			}),
            success:function(res){
             		if(!res.error){
             			_this.amount = res.count;
             			_this.IntegralDetailed = res.list;
             		}
           		  }
  				});
      		}else if(val=='全省'&&_this.userLevel=='provinceUser'){
      			$.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				conditions: {
					proAddress:_this.adress
				},     //查询条件，格式与mongodb查询条件相同
				pageNumber: 0,   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			}),
            success:function(res){
             		if(!res.error){
             			_this.amount = res.count;
             			_this.IntegralDetailed = res.list;
             		}
           		  }
  				});
      		}else  if(_this.userLevel=='provinceUser'){
      			 $.ajax({
  			type:"post",
    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
    		async:true,
    		dataType:'json',
    		data: JSON.stringify({
    			category:'Users',
				conditions: {
					townAddress:val
				},     //查询条件，格式与mongodb查询条件相同
				pageNumber: 0,   //页码，可选，从0起记
				pageSize: 10,    //页大小，可选
    			}),
            success:function(res){
             		if(!res.error){
             			_this.amount = res.count;
             			_this.IntegralDetailed = res.list;
             		}
           		  }
  				});
      		}
    	}
      },
    data() {
      return {	
      			"adress":'',
      			"search":'',
      			"slectdisc":'',
      			"userLevel":'',
      			"checked":false,
        		"amount":null,
        		'checkedCities':[],//以勾选的ID
        		'cityOptions':[],//所有的ID
        		'checkAll':false,
        		'isIndeterminate':false,
        		"IntegralDetailed":[
        		
        		],
        		options: [],
				        value8: '全国',
				      optionss:[
				      	{
				          value: 'groupUser',
				          label: '集团'
				        },
				        {
				          value: 'provinceUser',
				          label: '省公司'
				        },
				        {
				          value: 'personalUser',
				          label: '员工'
				        }
				      ]				    
      };
    }
  }
</script>
