// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Element from 'element-ui'
import App from './App'
import wangEditor from 'wangEditor'//编辑器

import './common/fontsize/iconfont.css'

//css
import 'element-ui/lib/theme-default/index.css'
import './common/css/reset.css'

//模块
import point from './components/pointManagement/point'//个人积分管理

import MyCase from './components/my-case/my-case'   //我的案例
import editor from './components/editor/editor'//个人编辑
import statiStic from './components/CaseStatistics/statistics'//案例统计
import RejectCase from './components/reject-case/reject-case'   //驳回案例
import AuditCase from './components/audit-case/audit-case'  //已审核案例
import NotAuditCase from './components/not-audit-case/not-audit-case'//未审核
import authority from './components/authorityManagement/AuthorityManagement'//权限管理
import CollectCase from './components/collect-case/collect-case'   //我的收藏
import pstatistics  from './components/poinman/ppstatistics'//省份集团积分处理
import startRevie  from './components/not-audit-case/to-audit'//开始审核预览
import AggregateStatistics from './components/CaseStatistics/aggregate-statistics'//聚合统计
import groupAggregateStatistics from './components/CaseStatistics/group-aggregate-statistics'  //集团聚合统计
import drafts from './components/drafts/Drafts'//草稿箱
import AccountInfo from './components/account-info/account-info'   //账号信息
import caseDetail from './components/caseDetail/case-detail'   //案例详情


Vue.use(VueRouter)//使用路由
Vue.use(VueResource)//使用HTTP请求
Vue.use(Element)//使用Element
const routes = [

{
		path:'/point',
		component:point//个人积分管理
},{
		path:'/editor',
		component:editor//个人编辑
},{
	  path:'/statistics',
		component:statiStic//案例统计
},{
		path:'/authority',
		component:authority//权限管理
},{
		path:'/MyCase',//我的案例
		component:MyCase,
},{
		path:'/MyCase/editor',
		component:editor//新增案例编辑
},{
    path: '/accountInfo',
    name: 'AccountInfo',
    component: AccountInfo
  },
  {
    path: '/rejectCase',
    name: 'RejectCase',
    component: RejectCase//驳回案例
  },
  {
    path: '/auditCase',
    name: 'AuditCase',
    component: AuditCase
  },//已审核案例
   {
    path: '/notAuditCase',
    name: 'NotAuditCase',
    component: NotAuditCase,
  }//未审核案例
  ,{
  	name:'startReview',
  	path:'/notAuditCase/startReview',
    	component:startRevie//开始审核
  },{
    path: '/collectCase',
    name: 'CollectCase',
    component: CollectCase
  },
  {
    path: '/pstaTistics',
    name: 'CollectCase',
    component: pstatistics
},
{
	path:'/statistics/aggregate-statistics',
	component:AggregateStatistics //聚合统计个人
},{
	path:'/statistics/group-aggregate-statistics',
	component:groupAggregateStatistics//聚合统计集团
},{
	path:'/statistics/MyCase/drafts',
	component:drafts//草稿箱
},{
	path:'/casecase/case-detail',
	component:caseDetail//草稿箱
}
];//路由配置

const router = new VueRouter({
  routes
});//实例化路由



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  ...App
}) //实例化vue
Vue.config.debug = true;


