<template>
	<div class="userCreat">
			 <div class="ui-content">
			 	
	    <div class="ui-dialog-box">
	    	<img class="closeCreat" src="../../common/img/icon_close.png" @click="closeCreat()"/>
	      <ul>
	        <li>
	          <label class="li-label">姓名：</label><input type="text" v-model="userInfo.name" placeholder="请输入姓名 "/>	
	        </li>
	        <li>
	          <label class="li-label">手机号：</label><input type="text" v-model="userInfo.phone" placeholder="手机号 "/>
	        </li>
	        <li class="select1">
	          <label class="li-label">省公司： </label><el-select v-model="value1"  placeholder="请选择" @change="selectPro(value1)">
				    <el-option
					      v-for="item in options1"
					      filterable=true
					      clearable=true
					      :label="item.label"
					      :value="item.value">
					    </el-option>
  				</el-select>
	        </li>
	        <li  class="select2">
	          <label class="li-label">地市公司： </label><el-select v-model="value2" placeholder="请选择">
					    <el-option
					      v-for="item in options2"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
  				</el-select>
	        </li>
	      </ul>
	      <div class="ui-footer" placeholder="请输入姓名 ">
	        <button @click="sureCreat()">确认创建</button>
	      </div>
	    </div>
	  </div>	
	</div>
</template>
<script type="text/javascript">
	import $ from 'jquery'; 
  export default {
  	mounted:function(){
  		var _this = this;
  			$.ajax({
			type:"post",
			url:"http://qdzy.internal-i-focusing.com/wif/data/query",
			async:true,
			dataType:'json',
			data:JSON.stringify({
				category: "Companies",
				conditions:{}
				}),
				success:function(res){
 						for(var a = 0;a<res.list.length;a++){
 					
 							var  mm = {
 										value:res.list[a].name,
 										label:res.list[a].name
 									};
 							_this.options1.push(mm);
 						};
				}
		});
  	},
    data () {
      return {
        userInfo:{
          name:'',
          phone:'',
          userLevel:'superAdmin',
          proAddress:'',
          townAddress:'',
          isInvited:false,
          isFreeze:false          
        },
        value3:'',
        options1:[{
				          value: '请选择',
				          label: '请选择'
				        }],
		value1: '',
		 options2: [{
				          value: '请选择',
				          label: '请选择'
				        }],
				        value2:'',
				      optionss:[
				      	{
				          value: '选项1',
				          label: '集团'
				        },
				        {
				          value: '选项2',
				          label: '省公司'
				        },
				        {
				          value: '选项3',
				          label: '员工'
				        }
				      ],
				      value:''				      
      }
    },
    methods:{
    	selectPro:function(val){
    		var _this = this;
    		this.options2=[];
    		this.value2 = null;
    	 $.ajax({
			type:"post",
			url:"http://qdzy.internal-i-focusing.com/wif/data/query",
			async:true,
			dataType:'json',
			data:JSON.stringify({
				category: "Companies",
				conditions:{name:val}
				}),
				success:function(ress){
					if(!ress.error){
						
						 	for(var a = 0;a<ress.list[0].cities.length;a++){
 						
 							var  mm = {
 										value:ress.list[0].cities[a],
 										label:ress.list[0].cities[a]
 								};
 							_this.options2.push(mm);
 						  };
						 					
					}				
				}
		});
    	},
    	sureCreat:function(){
   				$('.userCreat').hide();
 				var _this = this;   		
    				$.ajax({
    					type:"post",
    					url:"http://qdzy.internal-i-focusing.com/wif/data/create",
    					async:true,
    					dataType:'json',
    					data: JSON.stringify({category:'Users',content:{
        								name:_this.userInfo.name,
        								phone:_this.userInfo.phone,
        								userLevel:'provinceUser',
        								proAddress:_this.value1,
        								townAddress:_this.value2,
        								isInvited:false,
        								isFreeze:false
        			}}),
    					success:function(res){
    									if(!res.error){ 								
								    $.ajax({
								  			type:"post",
								    		url:"http://qdzy.internal-i-focusing.com/wif/data/query",
								    		async:true,
								    		dataType:'json',
								    		data: JSON.stringify({
								    			category:'Users',
								    			pageNumber: '',   //页码，可选，从0起记
												pageSize: 10,    //页大小，可选
								    			conditions:{
								    				userLevel:{$ne:'superAdmin'}
								    			},
								    			sort: {createTime: -1},
								    			}),
								             success:function(res){
								             	
								             		_this.$parent.IntegralDetailed = res.list;
								             		_this.$parent.amount = res.count;
								            }
								  		});		    						               				
    										$.ajax({
    											type:"post",
    											dataType:'json',
    											url:"http://qdzy.internal-i-focusing.com/easyChat/addSubscribeUsers",
    											async:true,
    											data: JSON.stringify([
    												{mobile:_this.userInfo.phone,remark:res.id}]
    												),
    											success:function(ress){
    												 if(!ress.error){
    												 	_this.userInfo.name = '';
    												 	_this.userInfo.phone = '';
    												 	_this.value1 = '';
    												 	_this.value2 = '';
    												 }
    											}
    										});
    									}else if(res.message=='The phone number existed.'||res.error){
    										  alert('该用户已经存在');
    									}
    					}
    				});
    		},
    		closeCreat:
    			function(){
    				$('.userCreat').hide();
    				this.userInfo.name = '';
    				this.userInfo.phone = '';
    				this.value1 = '';
    				this.value2 = '';
    			}
    		
    }
  }

</script>
<style scoped>
.userCreat{
			position: fixed;
			z-index: 100;
			background: rgba(0,0,0,.3);
			width: 100%;
			height: 100%;
			top: 0px;
			left: 0px;
			display: none;
	}
.ui-content{
    display:-webkit-box;
    -webkit-box-orient:horizontal;
    -webkit-box-pack:center;
    -webkit-box-align:center;
  }
.closeCreat{
	position: absolute;
	right: 10px;
	top: 10px;
	cursor: pointer;
}  
  .ui-dialog-box{
    border: 1px solid #ededed;
    background-color: #fff;
    box-shadow: 2px 2px 25px 1px rgba(0,0,0,.1);
    padding: 40px 100px;
    position: absolute;
    top: 25%;
    left: 30%;
    border-radius: 10px;
  }
  .ui-dialog-box ul{
    width: 100%;
  }
  .ui-dialog-box li{
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    color: #333333;
    border: 1px solid #ACACAC;
    border-radius: 5px;
    margin-bottom: 30px;
    padding: 0 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
  }
.ui-dialog-box li input{
	border: none;
	outline: none;
	height: 48px;
	font-size: 18px;
	text-align: center;
}
  .ui-footer{
    text-align: center;
  }
  .ui-footer button{
    width: 300px;
    height: 60px;
    background-color: #45a8dc;
    border-radius: 5px;
    color: #FFFFFF;
    font-size: 20px;
    border: none;
    margin-top: 10px;
    outline: none;
    cursor: pointer;
  }
  .ui-footer button:hover{
    background-color: #47A2DC;
  }
.li-label{
	display: inline-block;
	width: 100px;
}
.select2 .el-input__icon+.el-input__inner{
	padding-right: 135px!important;
}
</style>



