<!--驳回案例-->
<template>
  <div class="ui-content">
  	<div class="ui-content-box">
  		<div class="c-top fr">
      <el-select v-model="addValue" :placeholder="userInfo.userLevel=='groupUser'?'全国':'全省'" @change="getCaseDatas(0)">
        <el-option
          v-for="item in addOptions"
          :label="item.lable"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select v-model="classValue" placeholder="所有分类" @change="getCaseDatas(0)">
        <el-option
          v-for="item in classOptions"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <div class="fr ui-search">
        <i class="icon-search"></i>
        <input type="text" placeholder="案例名称" v-model="searchTitle">
        <button @click="getCaseDatas(0)">搜索</button>
      </div>
    </div>
    <div>
      <ul class="ui-case-list">
        <li v-for="el in dataList" style="height: 240px">
          <div class="ui-top" :style="{backgroundImage: 'url(http://qdzy.internal-i-focusing.com/getImage?id=' + el._id + ')'}" @click="toDetail(el._id)">
            <div class="ui-name">{{el.caseTitle}}</div>
          </div>
        </li>
      </ul>
    </div>
    <div class="ui-page" v-show="totalPage>6">
      <div class="block fr">
         <el-pagination
		      @current-change="handleCurrentChange"
		      :page-size="6"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
      </div>
    </div>
  		
  	</div>
    

  </div>

</template>
<script type="text/javascript">
	import $ from 'jquery';
  export default {
    name: 'RejectCase',
    data () {
      return {
      	domain:'http://qdzy.internal-i-focusing.com',
         addOptions: ['全国','西藏','浙江','福建','广东','广西','黑龙江','湖北','湖南','四川','宁夏','贵州','海南','北京','安徽','河北','河南','江苏','江西','内蒙古','山东','山西','陕西','上海','新疆','云南','重庆','甘肃','吉林','辽宁','青海','天津'],
        addValue: undefined,
        classValue: undefined,
        searchTitle:undefined,
       classOptions: [{
          value: '所有分类',
          label: '所有分类'
        }, {
          value: '规划拓展',
          label: '规划拓展'
        }, {
          value: '门店运营',
          label: '门店运营'
        }, {
          value: '销售策划',
          label: '销售策划'
        }, {
          value: '队伍建设',
          label: '队伍建设'
        }, {
          value: '数据统计',
          label: '数据统计'
        }],
//          模拟数据
        dataList:[],
        totalPage:0,
        getCaseDatas:'',
        userInfo:{}
      }
    },
    mounted:function(){
    	var _this=this;
    	
    	
    	
    	
    	
    	
    	
    	    
    $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	     console.log(ress.content.userLevel);
    					  	    
    				            if(ress.content.userLevel=='groupUser'){
    				               //$('#addVa').attr('placeholder','')
    				            	_this.addValue = '全国';
    				            	_this.addOptions=[{
    				            		lable:'全国',
    				            		value:'全国'
    				            	}]
    				            	$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{}
											}),
											success:function(res){
							 						for(var a = 0;a<res.list.length;a++){						 					
							 							var  mm = {
							 										value:res.list[a].name,
							 										label:res.list[a].name
							 									};
							 							_this.addOptions.push(mm);
							 						};
											}
									});
    				            }else{
    				            	_this.addValue = '全省';
    				            	_this.addOptions=[{
    				            		lable:'全省',
    				            		value:'全省'
    				            	}]
    				            	 $.ajax({
											type:"post",
											url:"http://qdzy.internal-i-focusing.com/wif/data/query",
											async:true,
											dataType:'json',
											data:JSON.stringify({
												category: "Companies",
												conditions:{name:ress.content.proAddress}
												}),
												success:function(ress){
													if(!ress.error){
														
														 	for(var a = 0;a<ress.list[0].cities.length;a++){
								 						
								 							var  mm = {
								 										value:ress.list[0].cities[a],
								 										label:ress.list[0].cities[a]
								 								};
								 							_this.addOptions.push(mm);
								 						  };
														 					
													}				
												}
										});
    				            }
    					}
    				});
    	
    	
    	
    	
    	
    	
    	
			//获取驳回案例数据
			_this.getCaseDatas=function(pageNo){
				var userType=_this.userInfo.userLevel;
					var proadd='',
						townadd='';		
				if(userType=='groupUser'){
					proadd=_this.addValue
					townadd=undefined
				}else{
					proadd=undefined
					townadd=_this.addValue
				}
				if(_this.addValue=='全国'||_this.addValue=='全省'){
					_this.addValue=undefined
				}
				if(_this.classValue=='所有分类'){
					_this.classValue=undefined
				}
				var _data={
					category: "Samples",
					filter: {caseImg: 0},
					conditions: {
						checkState: userType=='provinceUser'?3:8,
						'userInfo.proAddress':proadd==undefined?undefined:{$regex:_this.addValue},
						'userInfo.townAddress':townadd==undefined?undefined:{$regex:_this.addValue},
						caseType:_this.classValue,
						caseTitle:_this.searchTitle==undefined?undefined:{$regex:_this.searchTitle}						
					},
					pageNumber: pageNo,
					pageSize: 6,
					sort: {createTime: -1}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.dataList=rs.list;
						_this.totalPage=Math.ceil(rs.count/6);
						
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});						
			};
			//获取用户信息
			getUserInfo();
			function getUserInfo(){
					$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.userInfo=res.content;
						_this.getCaseDatas(0);
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
			};
    },
    methods:{
    	handleCurrentChange:function(x){
    		var _this=this;
    		_this.getCaseDatas(x-1);
    	},
    	toDetail:function(id){
    		this.$router.replace({path:'/casecase/case-detail',query:{id:id}})  		
    	},
    	
    },
  }
</script>
<style scoped>
  .ui-case-list  .ui-bottom{
    overflow: hidden;
  }
  .c-top{
    margin-bottom: 40px;
    overflow: hidden;
  }

  .ui-case-list .ui-bottom button{
    width: 50%;
    height: 50px;
    line-height: 50px;
    float: left;
    border: none;
    background: #ffffff;
    color: #000000;
    font-size: 16px;
    outline: none;
    cursor: pointer;
  }
  .ui-case-list .ui-name{
  	width: 84%;
  }
  .ui-case-list .ui-name span{
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 30px;
    background-color: #000000;
    margin-right: 10px;
  }
  .ui-case-list li{
    -webkit-border-bottom-left-radius: 10px;
    -webkit-border-bottom-right-radius: 10px;
  }
.ui-page{
	margin-top: 10px;
}
</style>



