<template>
	<div class="pointTable ui-content">
		<div class="ui-content-box">
			<div style="overflow: hidden;">		 		
		 		<div class="nav-btns fl">
					<div class="fl">
						<router-link to="/statistics/group-aggregate-statistics">聚合统计</router-link>
					</div>
					<div class="fl">
						案例导出
					</div>
				</div>
				
				<el-select v-model="value8" filterable :placeholder="userType=='groupUser'?'全国':'全省'"  class="selectW fl" @change="getDatas(0)">
				    <el-option
				      class="selectP"
				      v-for="item in options"
				      :label="item.lable"
				      :value="item.value">
				    </el-option>
	  			</el-select>
	  		
	  		
	  			<el-select v-model="value9" filterable placeholder="所有时间"  class="selectW fl" @change="getDatas(0)">
				    <el-option
				      class="selectP"
				      v-for="item in options1"
				      :label="item.lable"
				      :value="item.value">
				    </el-option>
	  			</el-select>
		 	</div>
			
  		
		<div class="taBle">				
			<ul>
				<li>上传日期</li>
				<li>地区</li>
				<li>案例名称</li>
				<li>审核信息</li>
			</ul>		
			<table border="1">
			  <tbody>
			    <tr v-for="item in IntegralDetailed">
			      <td>{{formatData(item.createTime)}}</td>
			      <td>{{item.userInfo.proAddress}}</td>
			      <td>{{item.userInfo.name}}</td>
			      <td>{{statusTransform(item.checkState)}}</td>
			    </tr>  
			  </tbody>
			</table>
		</div>	
	
  <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-size="10"
      layout="prev, pager, next"
      :total="totalPage">
    </el-pagination>
  </div>
		</div>
		 	
  
  <!--聚合统计-->
  <router-view></router-view>
	</div>
</template>
<script>
  import $ from "jquery";
  export default {
    methods: {
  		
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(x) {
        this.currentPage = x;
        this.getDatas(x-1);        
      },
      formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	},
    	//状态转换
    	statusTransform:function(x){
    		var txt='';
    		switch (x){
    			case 1:
	    			txt='未审核';
	    			break;
	    		case 2:
	    			txt='省公司开始审核';
	    			break;
	    		case 3:
	    			txt='省公司驳回';
	    			break;
	    		case 4:
	    			txt='省公司审核通过';
	    			break;
	    		case 5:
	    			txt='集团开始审核';
	    			break;
	    		case 6:
	    			txt='集团审核通过';
	    			break;
	    		case 7:
	    			txt='发布';
	    			break;
	    		case 8:
	    			txt='集团驳回';
	    			break;
	    			
	    		default:
	    			txt='未审核'
	    				    			
    		}
    		return txt;   		  			
  		},
      
    },
    mounted:function(){
    	
    	
    	var _this  = this;
    	
    				$.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	     console.log(ress.content.userLevel);
    					  	    _this.userType = ress.content.userLevel;
    				            if(ress.content.userLevel=='groupUser'){
    				               //$('#addVa').attr('placeholder','')
    				            	_this.value8 = '全国';
    				            	_this.options=[{
    				            		lable:'全国',
    				            		value:'全国'
    				            	}]
    				            	$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{}
											}),
											success:function(res){
							 						for(var a = 0;a<res.list.length;a++){						 					
							 							var  mm = {
							 										value:res.list[a].name,
							 										label:res.list[a].name
							 									};
							 							_this.options.push(mm);
							 						};
											}
									});
    				            }else{
    				            	_this.value8 = '全省';
    				            	_this.options=[{
    				            		lable:'全省',
    				            		value:'全省'
    				            	}]
    				            	 $.ajax({
											type:"post",
											url:"http://qdzy.internal-i-focusing.com/wif/data/query",
											async:true,
											dataType:'json',
											data:JSON.stringify({
												category: "Companies",
												conditions:{name:ress.content.proAddress}
												}),
												success:function(ress){
													if(!ress.error){
														
														 	for(var a = 0;a<ress.list[0].cities.length;a++){
								 						
								 							var  mm = {
								 										value:ress.list[0].cities[a],
								 										label:ress.list[0].cities[a]
								 								};
								 							_this.options.push(mm);
								 						  };
														 					
													}				
												}
										});
    				            }
    					}
    				});
			
			
    	
    	
    	
    	
    	var _this = this;
    	_this.getDatas=function(pageNo){
    				var proadd='',
						townadd='';				
				if(_this.userType=='groupUser'){
					proadd=_this.value8
					townadd=undefined
				}else{
					proadd=undefined
					townadd=_this.value8
				}
				if(_this.value8=='全国'||_this.value8=='全省'){
					_this.value8=undefined
				}
    		if(_this.value9=='所有时间'){
    			_this.value9=undefined
    		}
    		
    		$.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
         type: 'POST',
      	 dataType: 'JSON',
         data: JSON.stringify({
         					category: "Samples",
         					conditions: {
         						checkState: {$ne: 100},
         						'userInfo.proAddress':proadd==undefined?undefined:{$regex:_this.value8},
         						'userInfo.townAddress':townadd==undefined?undefined:{$regex:_this.value8},
      							createTime: _this.value9==undefined?undefined:{$gte:_this.value9[0],$lt: _this.value9[1]}
         					},     //查询条件，格式与mongodb查询条件相同
							pageNumber: pageNo,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							filter: {caseImg: 0},
							sort: {}          //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          console.log(res);
          _this.IntegralDetailed = res.list;
          _this.totalPage=res.count
      }
	  });
	    };
	    _this.getDatas(0);
    	
    },
    data() {
      return {
        		"amount":1000,
        		"IntegralDetailed":[
        		],
        		options: ['全国','西藏','浙江','福建','广东','广西','黑龙江','湖北','湖南','四川','宁夏','贵州','海南','北京','安徽','河北','河南','江苏','江西','内蒙古','山东','山西','陕西','上海','新疆','云南','重庆','甘肃','吉林','辽宁','青海','天津'],
				        value8: '全国'	,
				       options1: [{
				          value: undefined,
				          label: '所有时间'
				        }, {
				          value: [new Date().getTime()- 3600 * 1000 * 24 * 30,new Date().getTime()],
				          label: '最近一个月'
				        }, {
				          value: [new Date().getTime()- 3600 * 1000 * 24 * 90,new Date().getTime()],
				          label: '最近三个月'
				        }, {
				          value: [new Date().getTime()- 3600 * 1000 * 24 * 180,new Date().getTime()],
				          label: '最近半年'
				        }, {
				          value: [new Date().getTime()- 3600 * 1000 * 24 * 365,new Date().getTime()],
				          label: '最近一年'
				        }],
				        value9: '所有时间',
				        totalPage:0,
				        getDatas:'',
				        userType:''
      };
    }
  }
</script>
<style scoped>

.el-select{
	margin-right: 0;
}
.Sta{
	background: #45a8dc;
	border: none;
	width: 150px;
}
.Sta:hover{
	background: #409dcd;
}	
.taBle{
	width: 100%;
	height: 600px;
	margin-top: 40px;
}		
.pointTable ul{
	width: 100%;
	border-top-left-radius: 10px;
	border-top-right-radius:10px ;
	background: #d7d8da;
	height: 60px;
}	
.taBle	ul li{
		float: left;
		display: block;
		color: #333333;
		height: 60px;
		line-height: 60px;
		font-family:"微软雅黑";
		font-weight: 800;
		font-size: 18px;
	}
.taBle	ul li:first-of-type{
	 width: 15%;
	 text-align: center;
}	
.taBle	ul li:nth-of-type(2){
	 width: 15%;
	 text-align: center;
}	
.taBle	ul li:nth-of-type(3){
	 width: 55%;
}
.taBle	ul li:nth-of-type(4){
	 width: 15%;
}
.taBle	table{
		width: 100%;
	   border-radius:10px ;
	}
.taBle	tr{
		border: 1px solid #e5e5e5;
	
		height: 55px;
	}
.taBle	tr:nth-of-type(2n+1){
		background: #FAFAFA;
	}
.taBle	td{
	 	height: 55px;
	  	vertical-align: middle;
	  	font-size: 16px;
	  	color: #666666;
	}
.taBle td:first-of-type{
	 	width: 15%;
	 	text-align: center;
}
.taBle td:nth-of-type(2){
	 	width: 15%;
	 	text-align: center;
}
.taBle td:nth-of-type(3){
	 	width: 55%;
}
.taBle td:nth-of-type(){
	 	width: 15%;
}	
.block{
	float: right;
	margin-top: 40px;
}
.el-pager{
	margin: 0 20px 0 20px ;
}
.el-pager .number{
	height: 50px !important;
	width: 50px !important;
}
.el-pagination .btn-next, .el-pagination .btn-prev{
	height: 50px;
}
.el-pager li {
    padding: 0 4px;
    border-right: 0;
    background: #FFFFFF;
    font-size: 13px;
    min-width: 28px;
    height: 50px;
    line-height: 28px;
    text-align: center;
    margin: 0;
}
.selectP{
	font-size: 16px;
}
.selectW{
	width:200px;
	margin-left: 50px;
	display: inline-block;
	vertical-align: middle;
}
.nav-btns{
	width: 250px;
	height: 24px;
	border: none;
	border-radius: 5px;
	background: #45A8DC;
	color: #FFFFFF;
	overflow: hidden;
	padding: 10px 0;
	font-size: 16px;
}
.nav-btns div{
	width: 49%;
	height: 100%;
	line-height: 23px;
	cursor: pointer;
	background: #45A8DC;
	border: none;
	color: #FFFFFF;	
	border-right: 1px solid #2B88B9;
	outline: none;
	
}
.nav-btns div:last-child{
	border-right: none;
}
.nav-btns div a{
	color: #FFFFFF;
	text-decoration: none;
}
</style>



