<template>
	<div class="pointTable ui-content">
		<div class="ui-content-box">
			<div class="taBle">
				<h1>我的积分：<span class="jifen" style="color: #cc2929;"></span></h1>	
			<ul>
				<li>积分日期</li>
				<li>积分来源</li>
				<li>积分值</li>
			</ul>		
			<table border="1">
			  <tbody>
			    <tr v-for="item in IntegralDetailed">
			      <td>{{formatData(item.createTime)}}</td>
			      <td>{{item.reason}}</td>
			      <td>{{item.score}}</td>
			    </tr>  
			  </tbody>
			</table>
		</div>	
  <div class="block" v-show="amount>10">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-size="10"
      layout="prev, pager, next"
      :total="amount">
    </el-pagination>
  </div>
		</div>
		
	</div>
</template>
<script>
	import $ from 'jquery';
  export default {
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
      	var _this = this;
        this.currentPage = val;
         $.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
         type: 'POST',
      	 dataType: 'JSON',
         data: JSON.stringify({
         					category: "Statistics",
         					conditions: {'case.createUser':ress.content._id},     //查询条件，格式与mongodb查询条件相同
							pageNumber: val-1,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							sort: {createTime:-1}          //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          console.log(res);
          _this.IntegralDetailed = res.list;
      	 }
  	  });	
      },
       formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	}
    },
    mounted:function(){
    	var _this = this;
    	
    	
  	  //积分
 	$.ajax({
    	url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    	dataType:'json',
    	method:'post',
    	xhrFields:{withCredentials: true},
    	data:JSON.stringify({}),
    	success:function(ress){
    	if(!ress.error){
		//默认的第一页
		_this.user = ress.content._id;
    	$.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
         type: 'POST',
      	 dataType: 'JSON',
         data: JSON.stringify({
         					category: "Statistics",
         					conditions: {'case.createUser':ress.content._id},     //查询条件，格式与mongodb查询条件相同
							pageNumber: 0,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							sort: {createTime:-1}          //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          console.log(res);
          _this.IntegralDetailed = res.list;
      	 }
  	  });		  					  		
    			$.ajax({
				 	  	type:"post",
				 	  	url:"http://qdzy.internal-i-focusing.com/user/getUserScoreSummary",
				 	  	async:true,
				 	  	dataType:'json',
				 	  	data:JSON.stringify({id:ress.content._id}),
						success:function(resss){
							console.log(resss);
							if(!resss.error){
								$('.jifen').text(resss.score)
							}
						}
		 	  		});
    					  	}   			   
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    		});		   
    },
    data() {
      return {
        		"amount":null,
        		"user":'',
        		"IntegralDetailed":[
        		    /*{"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"},
        		    {"dataTime":"2017.01.08","soerce":"审批通过了","integralValue":"+10"}*/
        		]
      };
    }
  }
</script>
<style scoped>

.taBle{
	height: 690px;
}	
h1{
	font-size: 24px;
	margin-bottom: 40px;
	text-align: left;
}	
.pointTable ul{
	width: 100%;
	border-top-left-radius: 10px;
	border-top-right-radius:10px ;
	background: #d7d8da;
	height: 60px;
}	
	ul li{
		float: left;
		display: block;
		width: 33.33%;
		text-align: center;
		height: 60px;
		line-height: 60px;
		font-family:"微软雅黑";
		font-weight: 800;
		font-size: 20px;
	}
	table{
		width: 100%;
	   border-radius:100px ;
	}
	tr{
		border: 1px solid #e5e5e5;
		text-align: center;
		height: 55px;
	}
	tr:nth-of-type(2n+1){
		background: #fafafa;
	}
	td{
		width: 33.33%;
	 	height: 55px;
	  	vertical-align: middle;
	  	font-size: 16px;
	  	color: #666666;
	}
.block{
	float: right;
	margin-top: 40px;
}
.el-pager{
	margin: 0 20px 0 20px ;
}
.el-pager .number{
	height: 50px !important;
	width: 50px !important;
}
.el-pagination .btn-next, .el-pagination .btn-prev{
	height: 50px;
}
.el-pager li {
    padding: 0 4px;
    border-right: 0;
    background: #FFFFFF;
    font-size: 13px;
    min-width: 28px;
    height: 50px;
    line-height: 28px;
    text-align: center;
    margin: 0;
}
</style>



