<!--未审核案例-->
<template>
  <div class="ui-content">
  	<div class="ui-content-box">
  		  <div class="c-top fr">
      <el-select v-model="addValue" :placeholder="userInfo.userLevel=='groupUser'?'全国':'全省'"  @change="getCaseDatas(0)">
        <el-option        	
          v-for="item in addOptions"
          :label="item.lable"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select v-model="classValue" placeholder="所有分类" @change="getCaseDatas(0)">
        <el-option        	
          v-for="item in classOptions"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <div class="fr ui-search">
        <i class="icon-search"></i>
        <input type="text" placeholder="案例名称" v-model="searchTitle">
        <button @click="getCaseDatas(0)">搜索</button>
      </div>
    </div>
    <div>
      <ul class="ui-case-list">
        <li v-for="el in dataList">
          <div class="ui-top" :style="{backgroundImage: 'url(http://qdzy.internal-i-focusing.com/getImage?id=' + el._id + ')'}">
            <div class="ui-time" >{{formatData(el.createTime)}}</div>
            <div class="ui-name"><span class="pre-block" v-show='el.isPerfect'>精</span>{{el.caseTitle}}</div>
          </div>
          <div class="ui-bottom">
           <a @click="beginAudit(el._id)"><span>开始审核</span></a>
           <div class="btn-back-box" v-show='el.checkState==8'>
	           	<button class="txt-center bg-red btn-back">被驳回</button>
	           <div class="ui-back-reason">
	           		{{el.backReson}}          
	           </div>   
           </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="ui-page" v-show="totalPage>6">
      <div class="block fr">
        <el-pagination
		      @current-change="handleCurrentChange"
		      :page-size="6"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
      </div>
    </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
	import $ from 'jquery';

  export default {
    name: 'AuditCase',
    mounted:function(){
    	var _this=this;
    	
			$('.one-list-tree img').addClass('rota');			
			
			$.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	     console.log(ress.content.userLevel);
    					  	    
    				            if(ress.content.userLevel=='groupUser'){
    				               //$('#addVa').attr('placeholder','')
    				            	_this.addValue = '全国';
    				            	_this.addOptions=[{
    				            		lable:'全国',
    				            		value:'全国'
    				            	}]
    				            	$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{}
											}),
											success:function(res){
							 						for(var a = 0;a<res.list.length;a++){						 					
							 							var  mm = {
							 										value:res.list[a].name,
							 										label:res.list[a].name
							 									};
							 							_this.addOptions.push(mm);
							 						};
											}
									});
    				            }else{
    				            	_this.addValue = '全省';
    				            	_this.addOptions=[{
    				            		lable:'全省',
    				            		value:'全省'
    				            	}]
    				            	 $.ajax({
											type:"post",
											url:"http://qdzy.internal-i-focusing.com/wif/data/query",
											async:true,
											dataType:'json',
											data:JSON.stringify({
												category: "Companies",
												conditions:{name:ress.content.proAddress}
												}),
												success:function(ress){
													if(!ress.error){
														
														 	for(var a = 0;a<ress.list[0].cities.length;a++){
								 						
								 							var  mm = {
								 										value:ress.list[0].cities[a],
								 										label:ress.list[0].cities[a]
								 								};
								 							_this.addOptions.push(mm);
								 						  };
														 					
													}				
												}
										});
    				            }
    					}
    				});
			
			
			//获取未审核案例数据
			_this.getCaseDatas=function(pageNo){	
				var userType=_this.userInfo.userLevel
				var proadd='',
						townadd='';				
				if(userType=='groupUser'){
					proadd=_this.addValue
					townadd=undefined
				}else{
					proadd=undefined
					townadd=_this.addValue
				}
				if(_this.addValue=='全国'||_this.addValue=='全省'){
					_this.addValue=undefined
				}
				if(_this.classValue=='所有分类'){
					_this.classValue=undefined
				}							
				var _data={
					category: "Samples",
					conditions: {
						checkState: userType=='provinceUser'? {$in: [1,2,8]}: {$in: [4,5]},
						'userInfo.proAddress':proadd==undefined?undefined:{$regex:_this.addValue},
						'userInfo.townAddress':townadd==undefined?undefined:{$regex:_this.addValue},
						caseType:_this.classValue,
						caseTitle:_this.searchTitle==undefined?undefined:{$regex:_this.searchTitle}
					},
					filter: {caseImg: 0},
					pageNumber: pageNo,
					pageSize: 6,
					sort: {createTime: -1}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText)
						_this.dataList=rs.list
						_this.totalPage=rs.count
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});						
			};			
				//获取用户信息
			getUserInfo();
			function getUserInfo(){
					$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.userInfo=res.content;
						_this.getCaseDatas(0);
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
			};
			
			
    },
    methods:{
    	handleCurrentChange:function(x){
    		var _this=this;
    		_this.getCaseDatas(x-1);
    	},
    	
    	formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	},
    	beginAudit:function(caseId){
    		var _this=this;
    		var userType=_this.userInfo.userLevel
    		var _data={
					category:'Samples',
					content:{
						checkState:userType=='provinceUser'?2:5
					},
					id:caseId
				},
				url=_this.domain+'/wif/data/modify';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					dataType: 'json',
					data:JSON.stringify(_data),
					success:function(res){
						if(res.error==false){
							_this.$router.replace({path:'/notAuditCase/startReview',query:{ id:caseId}})
						}else{
							alert(res.message)
						}						
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});
    		
    	}
    },
    data () {
      return {
      	domain:'http://qdzy.internal-i-focusing.com',
      	searchTitle:undefined,
        addOptions: [],
        userInfo:{},
        addValue: '',
        classValue: '',
        classOptions: [{
          value: '所有分类',
          label: '所有分类'
        }, {
          value: '规划拓展',
          label: '规划拓展'
        }, {
          value: '门店运营',
          label: '门店运营'
        }, {
          value: '销售策划',
          label: '销售策划'
        }, {
          value: '队伍建设',
          label: '队伍建设'
        }, {
          value: '数据统计',
          label: '数据统计'
        }],
//          模拟数据
        dataList:[],
        totalPage:0,
        getCaseDatas:''
      }
    }
  }
</script>
<style scoped>
  /*.ui-case-list  .ui-bottom{
    overflow: hidden;
  }*/
  .c-top{
    margin-bottom: 40px;
    overflow: hidden;
  }

  .ui-case-list .ui-bottom{
    width: 100%;
    height: 60px;
    line-height: 60px;
    float: left;
    border: none;
    background: #ffffff;
    color: #333;
    font-size: 18px;
    outline: none;
    cursor: pointer;
    text-align: center;
  }
  .ui-case-list .ui-name span{
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 30px;
    margin-right: 5px;    
  }
  .ui-case-list .pre-block{
    background-color: rgba(0,0,0,.3);
  }
  .ui-case-list .ui-name{
    width: 84%;
  }
  .btn-back-box{
  	display: inline-block;
  	position: relative;
  	margin-left: 15%;
  }
  .btn-back{
  	padding: 0 10px;
    border-radius: 5px;
    border: 0;
    outline: none;
    color: #FFFFFF;
    font-size: 18px;
    height: 30px;
    line-height: 30px;
    
  }
.btn-back:hover + .ui-back-reason{
    display: block;
  }
   .txt-center{
    text-align: center;
  }
  .bg-red{
    background-color: #F85151!important;
  }
    .ui-back-reason{
    display: none;
    width: 110px;
    height: 60px;
    border-radius: 5px;
    border:1px solid #B5B6B6;
    position: absolute;
    left: -12%;
    top: -83px;
    background-color: rgba(255,255,255,.9);
    color: #333;
    font-size: 14px;
    padding: 15px 10px;
    line-height: 20px;
    z-index: 10;
    /*overflow-y: auto;*/
    text-align: left;
    /*background: url(images/bg_dialog_02.png)no-repeat center;*/
    background-size: 100% 100%;
    
  }
  .ui-back-reason:after{
    content: '';
    width: 18px;
    height: 18px;
    background: url("images/bg_dialog_01.png") no-repeat center;
    background-size: 100% 100%;
    position: absolute;
    bottom: -13px;
    left: 30px;
    z-index: 11;
  }
  
</style>



