<template>
 <div class="hello">
 	<div id="cropper-border">
 			<div class="container">
   			<!-- Wrap the image or canvas element with a block element (container) -->
				<div>
				  <img id="image" src="">
				</div>
  		</div>
  		<button class="call-off">取消</button>
  		<button class="sure-change">确认修改</button>
 	</div>
</div>
</template>
<script>
import $ from 'jquery';
import 'imports-loader?jQuery=jquery!../../../node_modules/cropper/dist/cropper.js';
import '../../../node_modules/cropper/dist/cropper.css';
export default {
  //name: 'hello',
  mounted:function(){
$('#image').cropper({
  aspectRatio: 740 / 400,   
  viewMode:2,//视图模式
  zoomable:true,//缩放 
  minCropBoxWidth:74,//最小比例宽度
  minCropBoxHeight:40,//最小比例高度
  dragMode:'move',//画布移动
  crop: function(e) {
  }
});


$('.call-off').click(function(){
			$('.hello').hide();
			$('.UploadImg').val('');
})

},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
	width: 500px;
	margin: 0 auto;
	margin-top:50px ;
}
img {
  max-width: 100%; /* This rule is very important, please do not ignore this! */
}
.hello{
	width: 100%;
	height: 100%;
	position: fixed;
	background: rgba(0,0,0,0.3);
	z-index: 1000;
	left: 0;
	top: 0;
}
#cropper-border{
		width: 620px;
		min-height: 470px;
		position: absolute;
		left: 30%;
		top: 15%;
		background: white;
}
#cropper-border button{
		width:200px;
		height: 50px;
		text-align: center;
		border: none;
		background: #d2d5d7;
		border-radius:5px ;
		outline: none;
		cursor: pointer;
		margin: 50px 0 50px 20px;
		font-size: 18px;
		color: white;
}
#cropper-border .call-off{
		margin-left: 100px;
}
#cropper-border .sure-change{
	background: #45A8DC;
}
#cropper-border .sure-change:hover{
	background: #50b8ee;
}

</style>



