<!--驳回案例-->
<template>
  <div class="ui-content">
  	<div class="ui-content-box">
  		<div class="c-top fr">
      <el-select v-model="classValue" placeholder="所有分类" @change="getCaseDatas(0)">
        <el-option
          v-for="item in classOptions"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <div class="fr ui-search">
        <i class="icon-search"></i>
        <input type="text" placeholder="案例名称" v-model="searchTitle">
        <button @click="getCaseDatas(0)">搜索</button>
      </div>
    </div>
    <div>
      <ul class="ui-case-list collectCase">
        <li v-for="el in dataList" style="height: 240px">
          <div class="ui-top" :style="{backgroundImage: 'url(http://qdzy.internal-i-focusing.com/getImage?id=' + el._id + ')'}" @click="toDetail(el._id)">
            <div class="icon-close" v-on:click="closeThisCase(el)"><i></i></div>
            <div class="ui-name"><span class="pre-red" v-show="el.isPerfect">精</span>{{el.caseTitle}}</div>
          </div>
        </li>
      </ul>
    </div>
    <div class="ui-page" v-show="totalPage>6">
      <div class="block fr">
       <el-pagination
		      @current-change="handleCurrentChange"
		      :page-size="6"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
      </div>
    </div>
  	</div>
  	<success></success>   

  </div>

</template>
<script type="text/javascript">
	import $ from 'jquery';
	import success from '../pop-up-box/succeed';
	
  export default {
    name: 'CollectCase',
    data () {
      return {
      	domain:'http://qdzy.internal-i-focusing.com',
      	totalPage:0,
        classValue: undefined,
        searchTitle:undefined,
        classOptions: [{
          value: '所有分类',
          label: '所有分类'
        }, {
          value: '规划拓展',
          label: '规划拓展'
        }, {
          value: '门店运营',
          label: '门店运营'
        }, {
          value: '销售策划',
          label: '销售策划'
        }, {
          value: '队伍建设',
          label: '队伍建设'
        }, {
          value: '数据统计',
          label: '数据统计'
        }],
        dataList:[],
        userInfo:{
          	openId:'',
          	id:''
       
          },
          getCaseDatas:'',
          curPageNo:0//当前页码
      }
    },
    components:{
			success
	 	},
    mounted:function(){
    	var _this=this;
    		//获取收藏案例数据
			_this.getCaseDatas=function (pageNo,openId){
				_this.curPageNo=pageNo;
				if(_this.classValue=='所有分类'){
					_this.classValue=undefined
				}
				var _data={
					openId:openId || _this.userInfo.openId,
					caseType:_this.classValue,
					caseTitle:_this.searchTitle==undefined?undefined:{$regex:_this.searchTitle}	,
					pageNumber: pageNo,
					pageSize: 6,
					sort: {createTime: -1}

//query接口
//					category: 'Samples',  
//					conditions: {"liked.openId":openId || _this.userInfo.openId},
//					filter: {caseImg: 0},//查询条件，格式与mongodb查询条件相同
//					pageNumber: pageNo,   //页码，可选，从0起记
//					pageSize: 6,    //页大小，可选
//					sort: {"createTime":-1} 

				},
//				url=_this.domain+'/wif/data/query';
				url=_this.domain+'/cusWifs/mob_web/getLike';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.dataList=rs.list;
						_this.totalPage=rs.count;
						
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});						
			};
    			//获取用户信息
			getUserInfo();
			function getUserInfo(){
					$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){					
						_this.userInfo.openId=res.content.openId
						_this.userInfo.id=res.content._id
						_this.getCaseDatas(0,res.content.openId);
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
			};
    	
		
    },
      methods:{
		    	handleCurrentChange:function(x){
		    		var _this=this;
		    		_this.getCaseDatas(x-1);
		    	},
	    	 closeThisCase:function(el){
	    	 	 event.stopPropagation();
	    	 	 console.log(111)
	    	 	var _this=this;
	          $.ajax({
	          	type:"post",
	          	url:_this.domain+'/cusWifs/mob_web/unlike',
	          	async:true,
	          	dataType:'json',
	          	data:JSON.stringify({
	          		caseId:el._id,
	          		userId:_this.userInfo.id	          		
	          	}),
	          	success:function(res){
	          		if(res.error==false){	      
	          			$('.shade1').fadeIn();
	    	 		 			$('#successMessage').text('删除成功！');	          			
	          		}
	          	},
	          	error:function(jqXhr,statusCode,statusText){
	          		
	          	}
	          });
	          $('.close').click(function(){
	          	$('.shade1').hide();
	    	 		 	_this.getCaseDatas(_this.curPageNo)
	    	 		 });
	        },
	        toDetail:function(id){
		    		this.$router.replace({path:'/casecase/case-detail',query:{id:id}})  		
		    	}
    },
  }

</script>
<style scoped>
  .ui-case-list  .ui-bottom{
    overflow: hidden;
  }
  .c-top{
    margin-bottom: 40px;
    overflow: hidden;
  }

  .ui-case-list .ui-bottom button{
    width: 50%;
    height: 50px;
    line-height: 50px;
    float: left;
    border: none;
    background: #ffffff;
    color: #000000;
    font-size: 16px;
    outline: none;
    cursor: pointer;
  }
  .ui-case-list .ui-name{
  	width: 84%;
  }
  .ui-case-list .ui-name span{
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 30px;
    margin-right: 5px;
  }
  .pre-red{
    background-color: #CC2929;
  }
.icon-close{
  width: 35px;
  height: 35px;
  border-radius: 35px;
  background-color:rgba(0,0,0,.2) ;
  text-align: center;
  position: absolute;
  right:10px;
  top: 10px;
  cursor: pointer;
  display: none;
}
  .icon-close i{
    display: inline-block;
    width: 15px;
    height: 15px;
    margin-top: 10px;
    background: url("images/icon_close_w.png") no-repeat center;
    background-size: 100% 100%;
  }
  .ui-case-list li{
    -webkit-border-bottom-left-radius: 10px;
    -webkit-border-bottom-right-radius: 10px;
  }
  .ui-case-list li:hover .icon-close{
    display: block;
  }
  .ui-page{
	margin-top: 10px;
}
</style>



