<template>
	<div>
		<div class="auditing-box" id="hehe">
		<!--<div id="wrapper">-->
			<div class="fl left" id="scroller">
			<h1>{{caseDetail.caseTitle}}</h1>
			<span class="ft-16 csae-type">{{caseDetail.caseType}}</span>
	      </el-select>
	      <p class="ft-12 hint-t">{{formatData(caseDetail.createTime)}}&nbsp;&nbsp;{{userInfo.proAddress}}&nbsp;&nbsp;<span>{{userInfo.name}}</span></p>
	      <div class="case-content" v-html="caseDetail.caseHtml"></div>
		</div>		
	</div>	
	</div>
	
	
</template>
<script type="text/javascript">
	import $ from 'jquery';
	
	export default {
    
     data () {
	     return {
	     	domain:'http://qdzy.internal-i-focusing.com',
	      	caseId:''	 ,
	      	caseDetail:{},
	      	userInfo:{}
	      }
	},
	mounted:function(){
		var _this=this;		
		//获取url上的id
			function getUrl(){
				var url=location.href;
				var _id=url.split('?')[1].split('=')[1];
				_this.caseId=_id;
				getCaseDatas(_id);				
			};	
		//获取该案例详情
			function getCaseDatas(id){
				var _data={
					category: "Samples",
					conditions: {
						_id:id
					}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.caseDetail=rs.list[0]
						_this.userInfo=rs.list[0].userInfo
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});						
			};
			getUrl();
	},
	methods:{
		formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	}
	}
	    
   }
</script>
<style type="text/css" scoped="scoped">
	#hehe{
		/*height: 800px;*/
		width: 1130px;
		position: relative;
	}
	.ui-content{
		padding: 0px;
		
	}
	.auditing-box{
		width: 100%;
		overflow: hidden;		
	}
	.auditing-box .left{
		width: 650px;
		box-shadow: 2px 2px 25px 1px rgba(0,0,0,.1);
		background: #FFFFFF;
		padding: 20px 100px;
	}
	.auditing-box h1{
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 20px;
	}
	.hint-t{
		color: #B2B2B2;
		padding:10px 10px 10px 0;		
	}
	.hint-t span{
		color: #4CAADD;
	}
	#scroller{
		padding-top: 40px;
		overflow-y: auto;
		min-height: 500px;
	}
	.csae-type{
		line-height: 30px;
		color: #676767;
	}
	
</style>




