<template>
	<div class="shade1">
		 <div class="success">
		 	<img class="close" src="../../common/img/icon_close.png"/>
		 	<div class="successImg">
		 		 	<img src="../../common/img/success.png"/>
		 		 	<img src="../../common/img/Failure.png" style="display: none;"/>
		 	</div>	
		 	<span id="successMessage">提交成功</span>
		 </div>
	</div>
</template>
<script>
	import $ from 'jquery'
	export default{
		  methods:{
		  	  /*closeMessage:function(){
		  	  	$('.shade1').hide();	
		  	  }*/
		  }
	}
</script>
<style scoped>
	.shade1{
	position: fixed;
	z-index: 100;
	background: rgba(0,0,0,.3);
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	display: none;

}
.success{
	width: 500px;
	height: 300px;
	background: white;
	margin: 18% auto;
	z-index: 10000;
	text-align: center;
	position: relative;
	border-radius: 10px;
}
.success span{
	font-size: 26px;
}
.successImg{
	height: 200px;
	overflow: hidden;
	width: 500px;
}
.successImg img{
display: block;
margin:  0 auto;
margin-top: 90px;
}
.close{
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
}
</style>


