<template>
	<div class="sha2">
		<div class="header-message">
			<div class="header-Mess">驳回理由</div>
			<div class="center-textarea">
				<textarea cols="50" rows="5" id="textarea" :ref="backReson" placeholder="请输入理由" v-model="backReson"></textarea>  
			</div>
			<div class="re-top">
				 <div class="re-top-1" @click="cancel">取消</div>
				 <div class="re-top-2" @click="onClickMe">确定</div>
			</div>
		</div>
	</div>
</template>
<script>
	import $ from 'jquery'
	export default{
		data(){
			return {
				backReson:''
			}
		},
		methods:{
			cancel:function(){
				$('.sha2').hide();
			},
			onClickMe: function(){
		        this.$emit('childevent',this.backReson);
		      }
		}
	}
</script>
<style scoped="scoped">
.sha2{
	position: fixed;
	z-index: 100;
	background: rgba(0,0,0,.3);
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	display: none;
}	 
 .header-message{
 	height: 390px;
 	width: 600px;
 	background: white;
 	position: absolute;
 	top: 20%;
 	left: 30%;
 	border-radius:5px ;
 }
.header-Mess{
	width: 600px;
	height: 80px;
	background: #45a8dc;
	font-size: 32px;
	text-align: center;
	line-height: 80px;
	color: white;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}
.center-textarea textarea{
	width: 600px;
	border: none;
	outline: none;
	font-size: 28px;
	resize:none;
	height: 230px;
}
.re-top{
	height: 80px;
	width: 600px;
	border-top: 1px solid #D9D9D9;
}
.re-top div{
	width: 49.5%;
	text-align: center;
	line-height: 80px;
	display: inline-block;
	font-size: 32px;
	cursor: pointer;
}
.re-top .re-top-1{
	border-right: 1px solid #D9D9D9;
	color: #AAAAAA;
}
.re-top .re-top-2{
	color: #45a8dc;
}
</style>



