<template>
	<div class="newEditor">
	  <p><input class="headline Width" type="text" placeholder="标题不超过30个字" v-model="caseInfo.caseTitle"/></p>
	  <p><textarea class="textArea Width" placeholder="内容摘要大于10文字" v-model="caseInfo.caseAbstract"></textarea></p>	
	  <p>
	  		<template>
					  <el-select v-model="caseInfo.caseType" clearable placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
			</template>
	  </p>
	  <p>
	   <el-button  type="primary" class="UImg">上传<i class="el-icon-upload el-icon--right"></i>
	     <input type="file" class="UploadImg">
	   </el-button><span style="color:#aaaaaa; margin-left: 20px;">要求像素720*400</span>
	  </p>
	  <p>
	  	 <div class="primaryImg" style="position: relative;">
	  	    图片<img v-bind:src="caseInfo.caseImg" style="width: 150px;height: 100px;position: absolute;top: 0px;left: 0px;"/>
	     </div>
	  </p>
	  <p>
	  				<!--用父容器来控制宽度-->
				<div style="width:1000px;">
				    <!--用当前元素来控制高度-->
				    <div id="div1" style="height:220px;max-height:500px;border-radius: 5px;overflow-y: auto;">
				        <!--<p>请输入内容...</p>-->
				    </div>
				</div>
	  </p>
	  <p>
	  			<el-button type="primary" class="ButtonColor" v-show='btnShow.save' @click="saveEdtor">保存</el-button>
	  	        <el-button type="primary" class="ButtonColor" v-show='btnShow.saveToDraft' @click="saveDrafs()">存草稿</el-button>
	  	        <el-button type="primary" class="ButtonColor" v-show='btnShow.reset' @click="resetCase()">重置</el-button>
	  	        <el-button type="primary" class="ButtonColor" v-show='btnShow.get' @click="subEditor">提交审核</el-button>
	  	        <el-button type="primary" class="ButtonColor12" v-on:click='openPywDialog()' v-show='btnShow.pvw'>预览</el-button>
	  	        <el-button type="primary" class="ButtonColor1" v-show='btnShow.back' @click="goBack()">返回</el-button>
	  </p>
	  <div class="pvw-box">
	  	
	  	<div class="pvw-dialog">
	  		<div class="bg-top">
	  			效果预览<i v-on:click="closePvwDialog()"></i>
	  		</div>
	  		<div class="pvw-info">
		  		<h1>{{caseInfo.caseTitle}}</h1>
				<span class="ft-16 csae-type">{{caseInfo.caseType}}</span>
		        <p class="ft-12 hint-t">2017.03.01&nbsp;&nbsp;成都市&nbsp;&nbsp;<span>张三</span></p>
		      <div class="case-content" v-html="caseInfo.caseHtml">		      	
	  		</div>	  	
	  		</div>
	  	</div>
	  </div>
	<crop></crop>
	<popup></popup>
	<success></success>
	</div>
</template>
<script>
	import $ from 'jquery';	
	import Vue from 'vue';
	import cropperss from './cropperss';//图片剪切
	import popUp from '../pop-up-box/pop-up';
	import success from '../pop-up-box/succeed';
	
	 export default {
	 	components:{
	 		crop:cropperss,
	 		popup:popUp,
			success
	 	},
	    data() {
	      return {
	      	domain:'http://qdzy.internal-i-focusing.com',
	      	caseInfo:{
	      		caseTitle:'',//案例的标题
		        caseAbstract:'',//案例的摘要
		        caseImg:'',//案例的封面图
		        caseType:'',//案例的类型
		        caseHtml:'请输入内容...'//案例的具体类容
	      	},
	        
	        options: [{
	          value: '规划拓展',
	          label: '规划拓展'
	        }, {
	          value: '门店运营',
	          label: '门店运营'
	        }, {
	          value: '销售策划',
	          label: '销售策划'
	        }, {
	          value: '队伍建设',
	          label: '队伍建设'
	        }, {
	          value: '数据统计',
	          label: '数据统计'
	        }],
	        caseId:'',
	        openPywDialog:function(){
	        	$('.pvw-box').show();	        	
	        },
	        closePvwDialog:function(){
	        	$('.pvw-box').hide();
	        },
	        btnShow:{
	        	save:false,
		        reset:true,
		        get:true,
		        pvw:true,
		        back:true,
		        saveToDraft:true
	        }
	      }
	    },
	    methods:{
	    	//重置
	    	resetCase:function(){
	    		var _this=this;
	    		_this.caseInfo={
	    			caseTitle:'',//案例的标题
			        caseAbstract:'',//案例的摘要
			        caseImg:'',//案例的封面图
			        caseType:'',//案例的类型
			        caseHtml:'请输入内容...'//案例的具体类容
	    		}
	    	},
	    	
	    	 	//返回    	 	
	    	 	goBack:function(){
	    	 		 var that = this;    	 		
	    	 			$('.shade').fadeIn();			
	      				 //that.$router.go(-1)
	      		if(location.href.substring(location.href.length-6)!='editor'){//不是新建案例
	      			if(location.href.split('?')[1].split('=')[0] == 'tag'){//如果是审核案例修改
	      				$('.shade .pop-is').text('是否保存该案例？');  
	      				$('.shade .sure').unbind('click');//防止点击重复绑定
	      				$('.callOff').unbind('click');
	      		     	$('.shade .sure').on('click',function(){//确认保存返回，会给后台发送请求
	      				that.$router.replace({ path: '/notAuditCase/startReview', query: { id:that.$route.query.tag}})
	      							 });
	      			 	$('.callOff').on('click',function(){
	      			 	that.$router.replace({ path: '/notAuditCase/startReview', query: { id:that.$route.query.tag}})
	      			 	});
	      			}else if(location.href.split('?')[1].split('=')[0] == 'caseId'){//如果是案例驳回再次编辑
	      				$('.shade .pop-is').text('是否提交该案例？'); 
	      				$('.shade .sure').unbind('click');//防止点击重复绑定
	      				$('.callOff').unbind('click');
	      		     	$('.shade .sure').on('click',function(){//确认保存返回，会给后台发送请求
	      				that.$router.replace({ path: '/MyCase'})
	      							 });
	      			 	$('.callOff').on('click',function(){
	      			 	that.$router.replace({ path: '/MyCase'})
	      			 	});
	      			}else if(location.href.split('?')[1].split('=')[0] == 'draID'){//草稿箱修改
	      				$('.shade .pop-is').text('是否提交该案例？'); 
	      				$('.shade .sure').unbind('click');//防止点击重复绑定
	      				$('.callOff').unbind('click');
	      		     	$('.shade .sure').on('click',function(){//确认保存返回，会给后台发送请求
	      				that.$router.replace({ path: '/statistics/MyCase/drafts'})
	      							 });
	      			 	$('.callOff').on('click',function(){
	      			 	that.$router.replace({ path: '/statistics/MyCase/drafts'})
	      			 	});
	      			};
	      		}else{//个人用户案例新建 
	      				$('.shade .pop-is').text('是否保存到草稿箱？'); 
	      				$('.shade .sure').unbind('click');//防止点击重复绑定
	      				$('.callOff').unbind('click');
	      				$('.shade .sure').on('click',function(){//确认保存返回，会给后台发送请求
	      				that.$router.replace({ path: '/MyCase'})
	      							 });
	      				$('.callOff').on('click',function(){
	      			 	 that.$router.replace({ path: '/MyCase'})
	      			  });
	      		  }; 	      	      				 				
	    	 	},
	    	 	//存草稿
	    	 	saveDrafs:function(){
	    	 		 var that = this;    
	    	 		 $('.shade1').fadeIn();
	    	 		 $('#successMessage').text('保存草稿箱成功！');
	    	 		 if(location.href.substring(location.href.length-6)!='editor'){
	    	 		 	if(location.href.split('?')[1].split('=')[0] == 'caseId'){
	    	 		 	$('.close').click(function(){
	    	 		 		that.$router.replace({ path: '/MyCase'})
	    	 			});
	    	 		 }else if(location.href.split('?')[1].split('=')[0] == 'draID'){
	    	 		 	$('.close').click(function(){
	    	 		 		that.$router.replace({ path: '/statistics/MyCase/drafts'})
	    	 			});
	    	 		 	}
	    	 		 }else{
	    	 		 	$('.close').click(function(){
	    	 	$.ajax({
						type:"post",
						url:"http://qdzy.internal-i-focusing.com/wif/data/create",
						 
						async:true,
						dateType:'json',
						data: JSON.stringify({
								category:'Samples',
								xhrFields:{
								withCredentials: true
									},
								content:{
										caseTitle:that.caseInfo.caseTitle,//案例title
	        							caseAbstract:that.caseInfo.caseAbstract,//案例的摘要
	        							caseImg:that.caseInfo.caseImg,//案例的封面图
	       								caseType:that.caseInfo.caseType,//案例的类型
	        							caseHtml:that.caseInfo.caseHtml,//案例的具体类容 
	        							isPerfect:false,//是否加精
	        							checkState: 100,//案例状态
	        							checkPoints: {province: 0,group: 0,publish: 0},//审核发布事件
	        							liked: []//收藏
        						}}),
     			    	success: function(res){
     			    		if(!res.error){
     			    			that.$router.replace({ path: '/MyCase'})
     			    		}    			    		
     			  		}
				  });	 	    	 		 		
	    	 			});
	    	 		 };
	    	 		 
	    	 	},
	    	 	//保存
	    	 	saveEdtor:function(){
	    	 		 var _this = this;    
	    	 		 console.log(_this.caseInfo)
	    	 		 var _data={
								category:'Samples',
								content:{
										caseTitle:_this.caseInfo.caseTitle,//案例title
	        							caseAbstract:_this.caseInfo.caseAbstract,//案例的摘要
	        							caseImg:_this.caseInfo.caseImg,//案例的封面图
	       								caseType:_this.caseInfo.caseType,//案例的类型
	        							caseHtml:_this.caseInfo.caseHtml//案例的具体类容 									
								},
								id:_this.caseId
							},
							url=_this.domain+'/wif/data/modify';
							$.ajax({
								type:"post",
								url:url,
								async:true,
								dataType: 'json',
								data:JSON.stringify(_data),
								success:function(res){
									if(res.error==false){
										 $('.shade1').fadeIn();
	    	 		 						$('#successMessage').text('保存成功！');
									}else{
										alert(res.message)
									}						
								},
								error:function(jqXhr,statusCode,statusText){
									alert(statusText)
								}
							});
	    	 		 
	    	 		 $('.close').click(function(){
	    	 		 	_this.$router.replace({ path: '/notAuditCase/startReview', query: { id:_this.$route.query.tag}})
	    	 		 });
	    	 	},
	    	 	//提交审核
	    	 	subEditor:function(){
	    	 		var that = this;
	    	 		$('.shade').fadeIn();
	    			$('.shade .pop-is').text('是否提交审核该案例？');
	    			$('.shade .sure').unbind('click');//防止点击重复绑定
	      			$('.callOff').unbind('click');
	      			$('.shade .sure').on('click',function(){//确认保存返回，会给后台发送请求
	      				
	      			$.ajax({
						type:"post",
						url:"http://qdzy.internal-i-focusing.com/wif/data/create",
						 
						async:true,
						dateType:'json',
						data: JSON.stringify({
								category:'Samples',
								xhrFields:{
								withCredentials: true
									},
								content:{
										caseTitle:that.caseInfo.caseTitle,//案例title
	        							caseAbstract:that.caseInfo.caseAbstract,//案例的摘要
	        							caseImg:that.caseInfo.caseImg,//案例的封面图
	       								caseType:that.caseInfo.caseType,//案例的类型
	        							caseHtml:that.caseInfo.caseHtml,//案例的具体类容 
	        							isPerfect:false,//是否加精
	        							checkState: 1,//案例状态
	        							checkPoints: {province: 0,group: 0,publish: 0},//审核发布事件
	        							liked: []//收藏
        						}}),
     			    	success: function(res){
     			    		if(!res.error){
     			    			that.$router.replace({ path: '/MyCase'})
     			    		}    			    		
     			  		}
					});	      					      					      				
	      					});
	      			$('.callOff').on('click',function(){
	      			 		$('.shade').fadeOut();
	      			   });
	    	 	}
	    },
		created:function(){
			var vm=this;		
			showbtn();
	    	//根据不同的入口显示不同的button	    	
	    	function showbtn(){
	    		//var obj={};
	    		if(location.href.substring(location.href.length-6)!='editor'){	    			
	    				var param=location.href.split('?')[1].split('=');	    		
	    		if(param[0]=='tag'){//编辑
	    			vm.btnShow.save=true;
	    			vm.btnShow.back=true;
	    			vm.btnShow.reset=false;
	    			vm.btnShow.get=false;
	    			vm.btnShow.pvw=false;
	    			vm.btnShow.saveToDraft=false;
	    		}else{
	    			vm.btnShow={
	    				save:false,
				        reset:true,
				        get:true,
				        pvw:true,
				        back:true,
				        saveToDraft:true
	    			}
	    		  }
	    		}    	
	    	};	      	
		},
	    mounted:function(){
	    	var _this = this;	    		   						
	    	$('#rightBox').css('background','#FFFFFF');
	    	$('.ui-content').hide();
	    	$('.hello').hide();
	    	$('.UploadImg').change(function(){
	    		var urll = window.URL.createObjectURL(document.getElementsByClassName('UploadImg')[0].files[0])	    		  
				$('#image').cropper(
					'replace',urll
					);
				$('.hello').show();
	    	});
	    	$('.sure-change').click(function(){
				var imgUrl = $('#image').cropper('getCroppedCanvas', {'width': 740,'height': 400})
				.toDataURL('image/jpeg');
				_this.caseInfo.caseImg = imgUrl;
				$.ajax({
					type:"post",
					url:"http://qdzy.internal-i-focusing.com/wif/data/create",
					 
					async:true,
					dateType:'json',
					data: JSON.stringify({
								category:'Images',
								content:{										
	        							caseImg:_this.caseInfo.caseImg,//案例的封面图       							
        				}}),
     			    success: function(res)
    				{
     			  }
				});
				$('.hello').hide();
			});
		  	var self = this;  	
          // 创建编辑器
            var editor = new wangEditor('div1');
            editor.config.uploadImgUrl = 'http://qdzy.internal-i-focusing.com/file/upload/getUrl';
            editor.config.menus =[
		        '|',
		        'bold',
		        'underline',
		        'italic',
		        'strikethrough',
		        'eraser',
		        'forecolor',
		        'bgcolor',
		        '|',
		        'quote',
		        'fontfamily',
		        'fontsize',
		      
		        'alignleft',
		        'aligncenter',
		        'alignright',
		        '|',
		        'link',
		        'unlink',
		        '|',
		        'img',
		        'video',
		        '|',
		        'undo',
		        'redo',
		        'fullscreen'
		    ];
            editor.onchange = function () {
            // onchange 事件中更新数据
                self.caseInfo.caseHtml = editor.$txt.html();
            };
            editor.create();
            //上传图片
            
            //根据caseID查询该案例的详情
            getcaseInfo();
            function getcaseInfo(){
            	var url=location.href
            	var caseId=url.split('=')[1]
            	console.log(caseId)
            	_this.caseId=caseId
            	_this.caseInfo={
	    			caseTitle:'',//案例的标题
			        caseAbstract:'',//案例的摘要
			        caseImg:'',//案例的封面图
			        caseType:'',//案例的类型
			        caseHtml:'请输入内容...'//案例的具体类容
	    		}
            	if(caseId==undefined){
            		return
            	}
            	var _data={
					category: "Samples",
					conditions: {
						_id:caseId
					}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					dataType:'JSON',
					success:function(res){
						_this.caseInfo=res.list[0]
						console.log(_this.caseInfo.caseHtml);
						$('#div1').html(_this.caseInfo.caseHtml);
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});
            }
            
    
	}
  }
	
</script>

<style scoped>
	p{
		margin-top: 20px;
	}
	.Width{
		width: 1000px;
		font-size:18px ;
		border-radius: 4px;
		
		border: none;
		border: 1px solid #CCCCCC;
	}
	.primaryImg{
		width: 150px;
		height: 100px;
		background: #f5f5f5;
		color: #aaaaaa;
		text-align: center;
		line-height: 100px;
		border: 1px solid #CCCCCC;
		border-radius: 5px;
	}
	.UImg{
		position: relative;
	}
	.UploadImg{
		position: absolute;
		width: 30px;
		left: 0px;
		top: 0px;
		z-index: 100;
		font-size:30px;
		opacity: 0;
	}
	.newEditor{
		margin: 40px 0 0 90px;
		min-width: 1000px;
	}
	.headline{
		height: 50px;
		text-indent: 1em;
	}
	.textArea{
		height: 100px;	
		resize: none;
		text-indent: 1em;
		padding: 10px 0;
	}
	.pvw-box{
		display: none;
		background: rgba(0,0,0,.4);
		width: 100%;
		height: 100%;
		position: fixed;
	    top: 0;
	    left: 0;
	    bottom: 0;
	    right: 0;
	    z-index: 99;
	}
	.wangEditor-container .wangEditor-txt{
		z-index: 1;
		border-radius: 5px;
		overflow: hidden;
	}
	
	.pvw-dialog{
		width: 415px;
		height: 667px;
		background: #FFFFFF;
		top: 10%;
		left: 30%;
		border-radius: 10px;
		overflow-x : hidden;
		overflow-y: auto;
		position: relative;
		
	}
	.pvw-info{
		padding:20px;
		margin-top: 35px;
	}
	.pvw-info h1{
		font-size: 20px;
		margin-bottom: 15px;
	}
	.pvw-info h3{
		margin-bottom: 20px;
	}
	.pvw-info .csae-type{
		line-height: 30px;
		color: #676767;
	}
	.pvw-box .bg-top{
		width: 415px;
		height: 50px;
		line-height: 50px;
		background: #47A8DD;
		text-align: center;
		color: #FFFFFF;
		font-size: 20px;
		position: fixed;
		top:8%;
		-webkit-border-top-left-radius: 10px;
		-webkit-border-top-right-radius: 10px;
		
	}
	.bg-top i{
		display: inline-block;
		width: 15px;
		height: 15px;
		background: url(../../common/img/icon_close_w.png) no-repeat center;
		background-size: 100% 100%;
		position: absolute;
		right: 10px;
		top: 17px;	
		cursor: pointer;
	}
	.el-button--primary{
		background: #45A8DC;
		cursor: pointer;
	}
	.el-button--primary:hover{
		background: #50B8EE;
	}
	.pvw-info .hint-t{
		color: #B2B2B2;
		margin-top: 10px;
	}
	
</style>
<style>
	.pvw-dialog .case-content img{		
		max-height: 350px;
		margin: 10px 0;
		max-width: 100%;
	}
	.pvw-dialog .case-content p{
		font-size: 14px;
		line-height: 25px;
		color: #7D7D7D;
		padding: 0 10px;
	}
	
	
</style>


