<template>
	<div class="auditing-box" id="hehe">
		<!--<div id="wrapper">-->
			<div class="fl left" id="scroller">
			<h1>{{caseDetail.caseTitle}}</h1>
			<el-select v-model="caseDetail.caseType" placeholder="规划拓展">
	        <el-option
	          v-for="item in classOptions"
	          :label="item.label"
	          :value="item.value">
	        </el-option>
	      </el-select>
	      <p class="ft-12 hint-t">{{formatData(caseDetail.createTime)}}&nbsp;&nbsp;{{userInfo.proAddress}}&nbsp;&nbsp;<span>{{userInfo.name}}</span></p>
	      <div class="case-content" v-html="caseDetail.caseHtml"></div>
		</div>
		<!--</div>-->
		<div class="fl right ui-btns">
			<div class="common-btn-blue" @click="auditSuccess">审核通过</div>
			<div class="common-btn-ccc" @click='toEdit'>再次编辑</div>
			<div class="common-btn-red" @click="auditRejected">审核驳回</div>
		</div>
	<rejecte @childevent="listenToMyBoy"></rejecte>
	<popup></popup>
	</div>	
</template>
<script type="text/javascript">
	
	import $ from 'jquery';
	import rejected from '../pop-up-box/Review-rejected';
	import popUp from '../pop-up-box/pop-up';
	
	function isPassive() {
    var supportsPassiveOption = false;
    try {
        addEventListener("test", null, Object.defineProperty({}, 'passive', {
            get: function () {
                supportsPassiveOption = true;
            }
        }));
    } catch(e) {}
    return supportsPassiveOption;
} 		
  export default {
    components:{
    	rejecte:rejected,
    	popup:popUp
    },
     data () {
      return {
      	domain:'http://qdzy.internal-i-focusing.com',
        caseType: '',
        classOptions: [{
          value: '所有分类',
          label: '所有分类'
        }, {
          value: '规划拓展',
          label: '规划拓展'
        }, {
          value: '门店运营',
          label: '门店运营'
        }, {
          value: '销售策划',
          label: '销售策划'
        }, {
          value: '队伍建设',
          label: '队伍建设'
        }, {
          value: '数据统计',
          label: '数据统计'
        }],
        caseDetail:{},
        userInfo:{},
        caseId:'',      
        backReson:''
        
      }
    },
    methods:{
    	listenToMyBoy: function (somedata){
              this.backReson = somedata
        },
    	auditRejected:function(){
    		var that = this;
    		var sta=that.caseDetail.checkState
    		 $('.sha2').show();
    		 $('.re-top-2').click(function(){//驳回
    		 	console.log(that.backReson)
    		 	if(that.backReson==''){
    		 		alert('必须填写驳回理由！');
    		 		return
    		 	}
    		 	  var _data={
					category:'Samples',
					content:{
						checkState:sta==2?3:8,
						backReson:that.backReson
					},
					id:that.caseId
				},
				url=that.domain+'/wif/data/modify';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					dataType: 'json',
					data:JSON.stringify(_data),
					success:function(res){
						if(res.error==false){
							that.$router.replace('/notAuditCase')
						}else{
							alert(res.message)
						}						
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});
    		 	  
    		 });
    	},
    	auditSuccess:function(){
    		var that = this;
    		$('.shade').fadeIn();
    		$('.shade .pop-is').text('确认审核通过？');
    		
    		$('.shade .sure').on('click',function(){//确认通过审核      	
    			var sta=that.caseDetail.checkState
      			var _data={
					category:'Samples',
					content:{
						checkState:sta==2?4:6,
						caseType:that.caseDetail.caseType
					},
					id:that.caseId
				},
				url=that.domain+'/wif/data/modify';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					dataType: 'json',
					data:JSON.stringify(_data),
					success:function(res){
						if(res.error==false){
							that.$router.replace('/notAuditCase')
						}else{
							alert(res.message)
						}
						
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});
      		});
      		$('.shade .callOff').click(function(){//取消通过审核
      			$('.shade').fadeOut();	
      		});
    	},
    	toEdit:function(){
    		this.$router.replace({ path: '/MyCase/editor', query: { tag:this.$route.query.id}})
    	},
    	formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	}
    	
    },
   
    mounted:function(){
    		var _this=this;
    	    $('#ui-contentt').hide();
//			var	myScroll = new IScroll('#wrapper', { bounceEasing: 'elastic', bounceTime: 0 ,bounce:false});
			getUrl();
			//获取url上的id
			function getUrl(){
				var url=location.href;
				var _id=url.split('?')[1].split('=')[1];
				_this.caseId=_id;
				getCaseDatas(_id);								
			};
			//获取该案例详情
			function getCaseDatas(id){
				var _data={
					category: "Samples",
					conditions: {
						_id:id
					}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.caseDetail=rs.list[0]
						_this.userInfo=rs.list[0].userInfo
					},
					error:function(jqXhr,statusCode,statusText){
						alert(statusText)
					}
				});						
			};
			
			

		},	
  }
</script>
<style scoped="scoped">
	#hehe{
		/*height: 800px;*/
		width: 1130px;
		position: relative;
	}
	.ui-content{
		padding: 0px;
		
	}
	.auditing-box{
		width: 100%;
		overflow: hidden;		
	}
	.auditing-box .left{
		width: 650px;
		box-shadow: 2px 2px 25px 1px rgba(0,0,0,.1);
		background: #FFFFFF;
		padding: 20px 100px;
	}
	.auditing-box h1{
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 20px;
	}
	.hint-t{
		color: #B2B2B2;
		padding:10px;		
	}
	.hint-t span{
		color: #4CAADD;
	}
	
	.ui-btns{
		padding: 50px 30px;
		width: 200px;
		float: right;
	}
	.ui-btns .d-btn{
		width: 120px;
		height: 35px;
		line-height: 33px;
		text-align: center;
		border: none;
		border-radius: 5px;
		margin: 10px 0;
		color: #FFFFFF;
		cursor: pointer;
		outline: none;
	}
	.bg-blur{
		background: #45A8DC;
	}
	.bg-block{
		background: #2F323A;
	}
	.bg-block a{
		color:#fff;
		display:inline-block;
		width:100%;
		
	}
	.bg-red{
		background: #F85151;
	}
	#scroller{
		padding-top: 40px;
		overflow-y: auto;
		min-height: 500px;
	}
</style>
<style type="text/css">
	.auditing-box .case-content{
		padding-top: 20px;
	}
	.auditing-box .case-content img{
		/*width: 100%;
		height: 100%;*/
		max-height: 350px;
		margin: 10px 0;
	}
	.auditing-box .case-content p{
		font-size: 14px;
		line-height: 25px;
		color: #7D7D7D;
		padding: 0 10px;
	}
	.auditing-box .case-content h2{
		font-size: 18px;
		line-height: 30px;
	}
</style>


