<template>
  <div class="ui-content">
    <div class="ui-dialog-box">
      <ul>
        <li>
          姓名：{{userInfo.name}}
        </li>
        <li>
          手机号：{{userInfo.phone}}
        </li>
        <li>
          省公司：{{userInfo.proAddress}}
        </li>
        <li>
          地市公司：{{userInfo.townAddress}}
        </li>
      </ul>
      <div class="ui-footer">
        <div class="_btn"><a href="http://qdzy.internal-i-focusing.com/user/signOut" style="color: #FFFFFF;">&nbsp;&nbsp;退出账号</a></div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
	import $ from 'jquery';
  export default {
    name: 'AccountInfo',
    data () {
      return {
        userInfo:{}
      }
    },
    mounted:function(){
    	 			var _this = this;
    				$.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(res){
    					  	console.log(res);
    					  	_this.userInfo = res.content;
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    				});
    			 /* this.$http.post('http://qdzy.internal-i-focusing.com/user/getCurrentUser',[{}]).then(
    				function(res){
    					  console.log(res.body.content);
    					  this.userInfo = res.body.content;
    				}, function(erro){
    					
    				});*/
    }
  }

</script>
<style scoped>
  .ui-content{
    display:-webkit-box;
    -webkit-box-orient:horizontal;
    -webkit-box-pack:center;
    -webkit-box-align:center;
  }
  .ui-dialog-box{
    width: 500px;
    border: 1px solid #ededed;
    background-color: #fff;
    box-shadow: 2px 2px 25px 1px rgba(0,0,0,.1);
    padding: 80px 100px;
    position: relative;
    top: 80px;
    border-radius: 10px;
  }
  .ui-dialog-box ul{
    width: 100%;
  }
  .ui-dialog-box li{
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    color: #333333;
    border: 1px solid #ACACAC;
    border-radius: 5px;
    margin-bottom: 30px;
    padding: 0 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
  }

  .ui-footer{
    text-align: center;
  }
  .ui-footer ._btn{
    width: 300px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    background-color: #45a8dc;
    border-radius: 5px;
    color: #FFFFFF;
    font-size: 20px;
    border: none;
    margin-top: 10px;
    outline: none;
    cursor: pointer;
    display: inline-block;
  }
  .ui-footer button:hover{
    background-color: #47A2DC;
  }
</style>



