<template>
	<div class="Dra-table ui-content">
		<div class="ui-content-box">
			<div class="headerTop">
	
		  	<el-button type="primary" class="fr Dra-button" size="mini">
		  		<router-link to='/MyCase'>返回</router-link>
		  	</el-button>
		  	<el-button type="primary" class="fr Dra-button" size="mini" @click="delThisCase()">删除</el-button>	
		  		  	<!--<el-checkbox v-model="checked" class="allChecked">全选</el-checkbox>-->
		    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" class="allChecked" @change="handleCheckAllChange">全选</el-checkbox>
		  </div>
		  <div class="taBlee">
			<ul>
				<li>&nbsp;</li>
				<li class="txt-left">案例名称</li>
				<li>编辑时间</li>
				<li>&nbsp;</li>
			</ul>	
			<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" class="hehe"> 
			<table border="1">
			  <tbody>			  		
				    <tr v-for="item in IntegralDetailed">
				      <td>
				      	<el-checkbox :label="item._id" >&nbsp;</el-checkbox>
				      </td>
				      <td class="txt-left"><span>{{item.caseTitle}}</span></td>
				      <td>{{formatData(item.createTime)}}</td>
				      <td class="iconfont"><i  class="icon-shanchu" @click="deletThis(item._id)"></i>&nbsp;&nbsp;<i class="icon-wodedingdan30" @click="editorthis(item._id)"></i></td>
				    </tr> 			
			  </tbody>
			</table>
			</el-checkbox-group>
		</div>	
	<div class="ui-page" v-show="amount>10">
        <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :page-size="10"
		      layout="prev, pager, next"
		      :total="amount">
		    </el-pagination>
      </div>
		</div>
		  
		  <delDialog></delDialog>
	</div>
</template>
<style scoped>
i{
	font-style: normal;
}
.hehe{
	width: 100% !important;
}
.iconfont{
	color: #dddddd;
	font-size: 28px !important;
}
i{
	cursor: pointer;
}
.headerTop{
	height: 57px;
	width: 100%;
}
.Dra-button{
	width: 90px;
	height: 45px;
	font-size: 16px;
	margin: 0px 30px 0 0 !important
}
.Dra-button a{
	color: #FFFFFF;
	display: inline-block;
	width: 100%;
}
.Dra-button:nth-of-type(1){
	background: #d2d5d7;
	border: none;
}
.Dra-button:first-of-type{
	margin-right: 0 !important;
}
.Dra-table{
	width: 100%;
}
.taBlee{
	max-width: 100%;
	width: 100%;
	height: 670px;
}
.taBlee ul{
	height: 60px;
	background: #d7d8da;
	border-top-left-radius:5px;
	border-top-right-radius: 5px;
	margin-top: 30px;
}
.taBlee ul li{
	float: left;
	font-size: 18px;
	color: #333333;
	line-height: 60px;
	height: 60px;
	display:block;
	font-weight: bold;
}
.allChecked{
	float: right;
	font-size: 18px !important;
	line-height: 45px;
	margin-right: 50px;
}
.taBlee ul li:first-of-type,.taBlee table td:first-of-type{
	width: 13%;
	text-align: center;
	
}
.taBlee ul li:nth-of-type(2),.taBlee table td:nth-of-type(2){
	width: 50%;
	
}
.taBlee ul li:nth-of-type(3),.taBlee table td:nth-of-type(3){
	width: 18%;
	line-height: 49px;
	
}
.taBlee table{
	width: 100%;
	border: 1px solid #e5e5e5;
	border-top: none;
	position: relative;
	top: -6px;
}
.taBlee table tr{
	height: 55px;
}
.taBlee table td{
	height: 55px;
	border-bottom: 1px solid  #e5e5e5;
	line-height: 55px;
	overflow: hidden;
}
.taBlee table tr:nth-of-type(2n+1){
	background: #fafafa;
}
.ui-page{
	margin: 10px 0 10px 0;
	float: right;
}
</style>
<script>
	
	import $ from 'jquery'
	import delDialog from '../pop-up-box/pop-up'
	
	export default {
	created:function(){
		//全选
  		
  	},
  	mounted:function(){
  		var _this = this;
  		
  		$.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(res){
    					  	console.log(res.content._id);
    					  	_this.users = res.content._id;
    					  	  		
    	$.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
         type: 'POST',
      	 dataType: 'JSON',
      	 
         data: JSON.stringify({
         					category: "Samples",
         					filter: {caseImg: 0},
         					conditions: {createUser:res.content._id,checkState:100},     //查询条件，格式与mongodb查询条件相同
							pageNumber: 0,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							sort: {createTime: -1}        //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          if(!res.error){
          	 	_this.IntegralDetailed = res.list;
          	 	_this.amount = res.count;
          	for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);//加入用户的ID
  			};
          }
          console.log(res);        
 	    }
  	});
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    				});
  		

  	},
	components:{
  		delDialog
  	},
    methods: {
    	formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	},
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
      	var _this = this;
        this.currentPage = val;
        console.log(val);
        console.log(`当前页: ${val}`);
        $.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
         type: 'POST',
      	 dataType: 'JSON',     	 
         data: JSON.stringify({
         					category: "Samples",
         					filter: {caseImg: 0},
