							pageSize: 10,    //页大小，可选
							sort: {createTime: -1}        //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          if(!res.error){
          	 	_this.IntegralDetailed = res.list;
          	for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);//加入用户的ID
  			};
          }
          console.log(res);        
 	    }
  	});
      },
      delThisCase:function(){
      	var _this = this; 	
      	$('.shade').fadeIn()
      	$('.shade .pop-is').text('是否删除案例？');
      	$('.sure').click(function(){     		    	
 	        $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/wif/data/delete",
    					  dataType:'json',
    					  method:'post',  					  
    					  data:JSON.stringify({
    					  		category: 'Samples',//指明需要删除的实体类型
    					  		ids:_this.checkedCities//指明需要删除的实体ID
    					  }),						          
    					  xhrFields:{withCredentials: true},
    					  success:function(res){
    			       			console.log(res);
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    			});	
      	});
      },
       handleCheckAllChange(event) {
        this.checkedCities = event.target.checked ? this.cityOptions : [];
        this.isIndeterminate = false;
        console.log(this.checkedCities);
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cityOptions.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cityOptions.length;
        console.log(this.checkedCities);
      },      
      //删除
      deletThis:function(num){
      	   console.log(num);
      	   var that = this;
      	   var _this = this;
      	   $('.sure').unbind('click');//取消重复绑定
      	   $('.callOff').unbind('click');
      	   $('.shade').fadeIn();
