         data: JSON.stringify({
         					category: "Samples",
         					filter: {caseImg: 0},
         					conditions: {createUser:_this.$parent._userId,checkState:100},     //查询条件，格式与mongodb查询条件相同
							pageNumber: 0,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							sort: {createTime: -1}        //排序条件，可选，格式与mongodb同。),
    			}),
    	success: function(res){
          //..do something.
          if(!res.error){
          	 	_this.IntegralDetailed = res.list;
          	 	_this.amount = res.count;
          	for(var a = 0;a < _this.IntegralDetailed.length;a ++){
  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);//加入用户的ID
  			};
          }
          console.log(res);        
 	    }
  	});
    			}
            }
  		});
      	   	/*      $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/wif/data/delete",
    					  dataType:'json',
    					  method:'post',  
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({
    					  		category: 'Samples',//指明需要删除的实体类型
    					  		id:num//指明需要删除的实体ID
    					  }),						          					
    					  success:function(res){
    			       			console.log(res);
    			       			if(!res.error){
    $.ajax({url: 'http://qdzy.internal-i-focusing.com/wif/data/query',
		         type: 'POST',
		      	 dataType: 'JSON',
		      	 
		         data: JSON.stringify({
		         			category: "Samples",
		         		    filter: {caseImg: 0},
		         			conditions: {createUser:_this.$parent._userId,checkState:100},     //查询条件，格式与mongodb查询条件相同
							pageNumber: 0,   //页码，可选，从0起记
							pageSize: 10,    //页大小，可选
							sort: {createTime: -1}        //排序条件，可选，格式与mongodb同。),
		    			}),
		    	success: function(res){
		          //..do something.
		          if(!res.error){
		          	 	_this.IntegralDetailed = res.list;
		          	 	_this.amount = res.count;
		          	for(var a = 0;a < _this.IntegralDetailed.length;a ++){
		  				_this.cityOptions.push(_this.IntegralDetailed[a]._id);//加入用户的ID
		  			};
		          }
          			console.log(res);        
 	   		 }
  			});
    			       			}
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    			});	
	      	  */
      	   });
      	   $('.callOff').click(function(){
      	   		 $('.shade').fadeOut();
      	   });
      	  
      },
      editorthis:function(num){
      	  this.$router.replace({path:'/MyCase/editor',query:{draID:num}})
      }
    },
    data() {
      return {  
      	 		"checked":false,
        		"amount":null,
        		'checkedCities':[],//以勾选的ID
        		'cityOptions':[],//所有的ID
        		'checkAll':false,
        		'isIndeterminate':false,
        		"IntegralDetailed":[],
        		'users':''
      };
    }
  }
</script>


