<!--已审核案例-->
<template>
  <div class="ui-content">
  	<div class="ui-content-box">
  		<div class="c-top fr">
      <el-select v-model="addValue" :placeholder="admin.userLevel=='groupUser'?'全国':'全省'" @change="getCaseDatas(0)">
        <el-option
          v-for="item in addOptions"
          :label="item.lable"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select v-model="classValue" placeholder="所有分类" @change="getCaseDatas(0)">
        <el-option
          v-for="item in classOptions"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <div class="fr ui-search">
        <i class="icon-search"></i>
        <input type="text" placeholder="案例名称" v-model="searchTitle">
        <button @click="getCaseDatas(0)">搜索</button>
      </div>
    </div>
    <div>
      <ul class="ui-case-list">
        <li v-for="el in dataList" v-bind:style="{height:liHeight}">
          <div class="ui-top" :style="{backgroundImage: 'url(http://qdzy.internal-i-focusing.com/getImage?id=' + el._id + ')'}" @click="toDetail(el._id)">
            <div class="ui-time">{{formatData(el.createTime)}}</div>
            <div class="ui-name"><span class="pre-block" v-show='el.isPerfect'>精</span>{{el.caseTitle}}</div>
          </div>
          <div class="ui-bottom" v-show="admin.userLevel=='groupUser'">
            <button @click="publish(el)">{{el.checkState==7?'已发布':'发布案例'}}</button>
            <button @click="addDigest(el)" v-show="!el.isPerfect">加精</button>
          </div>
        </li>
      </ul>
    </div>
    <div class="ui-page" v-show="totalPage>6">
      <div class="block fr">
        <el-pagination
		      @current-change="handleCurrentChange"
		      :page-size="6"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
      </div>
    </div>
  	</div>
    
<pop></pop>
  </div>

</template>
<script type="text/javascript">
	import pop from '../pop-up-box/pop-up';
	import $ from 'jquery';
  export default {
    name: 'AuditCase',
    
    data () {
      return {
      	domain:'http://qdzy.internal-i-focusing.com',
         addOptions: [],
        addValue: undefined,
        classValue: undefined,
        searchTitle:undefined,
        classOptions: [{
          value: '所有分类',
          label: '所有分类'
        }, {
          value: '规划拓展',
          label: '规划拓展'
        }, {
          value: '门店运营',
          label: '门店运营'
        }, {
          value: '销售策划',
          label: '销售策划'
        }, {
          value: '队伍建设',
          label: '队伍建设'
        }, {
          value: '数据统计',
          label: '数据统计'
        }],
//          模拟数据
        dataList:[],
        totalPage:0,
        admin:{},//账号信息
        liHeight:'300px',
        getCaseDatas:'',//获取list
        curPageNo:0//当前页码
        
      }
    },
    methods:{
    	//发布案例
    	publish:function(el){ 
    			var _this=this;
    			
    			if(el.checkState==7){
    				$('.shade').hide();
    				return
    				
    			}
    			$('.shade').fadeIn();
    			$('.shade .pop-is').text("确认发布案例？");
    			$('.shade .sure').unbind('click');//防止事件重复绑定
    			$('.shade .callOff').unbind('click')
    			$('.shade .sure').on('click',function(){//确认发布案例
							var _data={
								category:'Samples',
								filter: {caseImg: 0},
								content:{
									checkState:7
								},
								id:el._id								
							},
							url1=_this.domain+'/wif/data/modify';
							$.ajax({
								type:"post",
								url:url1,
								async:true,
								dataType: 'json',
								data:JSON.stringify(_data),
								success:function(res){
									$('.shade').fadeOut();
									//发布成功之后加10积分
									var addJf={
										category:'Statistics',
										content:{
											caseId:el._id,
											score:10,
											type:'pass',
											reason:'发布成功',
											district:el.userInfo.proAddress
										}	
									},
									url2=_this.domain+'/wif/data/create';
									$.ajax({
										type:"post",
										url:url2,
										data:JSON.stringify(addJf),
										async:true,
										success:function(res){
											_this.getCaseDatas(_this.curPageNo);
										},
										error:function(jqXhr,statusCode,statusText){
											alert(statusText)
										}
									});
								},
								error:function(jqXhr,statusCode,statusText){
									alert(jqXhr,statusCode,statusText)									
								}
							});
      		});
      		$('.shade .callOff').on('click',function(){//取消通过审核
      			   $('.shade').fadeOut();
      		});
    	},
    	//加精案例
    	addDigest:function(el){
    			var _this=this;    		
    		  $('.shade .sure').unbind('click');//防止事件重复绑定
    			$('.shade .callOff').unbind('click')//防止事件重复绑定
    			$('.shade').show();
    			$('.shade .pop-is').text("确认案例加精？");
    			$('.shade .sure').on('click',function(){//确认加精

							var addData={
								category:'Statistics',
								content:{
									caseId:el._id,
									score:20,
									type:'perfect',
									reason:'加精',
									district:el.userInfo.proAddress
								}								
							},
							url=_this.domain+'/wif/data/create';
							$.ajax({
								type:"post",
								url:url,
								data:JSON.stringify(addData),
								async:true,
								success:function(res){
									$('.shade').fadeOut();
									_this.getCaseDatas(_this.curPageNo);
								},
								error:function(jqXhr,statusCode,statusText){
									alert(statusText)
								}
							});

      		});
      		$('.shade .callOff').on('click',function(){//取消加精
      			   $('.shade').fadeOut();
      		});
    	},
    	handleCurrentChange:function(x){
    		var _this=this;
    		_this.getCaseDatas(x-1);
    	},
    	formatData:function(date){    
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	},
    	toDetail:function(id){    		
    		this.$router.replace({path:'/casecase/case-detail',query:{id:id}})
    	}
    },
    mounted:function(){
    	var _this=this;
    
    
    
    
    
    
    $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	     console.log(ress.content.userLevel);
    					  	    
    				            if(ress.content.userLevel=='groupUser'){
    				               //$('#addVa').attr('placeholder','')
    				            	_this.addValue = '全国';
    				            	_this.addOptions=[{
    				            		lable:'全国',
    				            		value:'全国'
    				            	}]
    				            	$.ajax({
										type:"post",
										url:"http://qdzy.internal-i-focusing.com/wif/data/query",
										async:true,
										dataType:'json',
										data:JSON.stringify({
											category: "Companies",
											conditions:{}
											}),
											success:function(res){
							 						for(var a = 0;a<res.list.length;a++){						 					
							 							var  mm = {
							 										value:res.list[a].name,
							 										label:res.list[a].name
							 									};
							 							_this.addOptions.push(mm);
							 						};
											}
									});
    				            }else{
    				            	_this.addValue = '全省';
    				            	_this.addOptions=[{
    				            		lable:'全省',
    				            		value:'全省'
    				            	}]
    				            	 $.ajax({
											type:"post",
											url:"http://qdzy.internal-i-focusing.com/wif/data/query",
											async:true,
											dataType:'json',
											data:JSON.stringify({
												category: "Companies",
												conditions:{name:ress.content.proAddress}
												}),
												success:function(ress){
													if(!ress.error){
														
														 	for(var a = 0;a<ress.list[0].cities.length;a++){
								 						
								 							var  mm = {
								 										value:ress.list[0].cities[a],
								 										label:ress.list[0].cities[a]
								 								};
								 							_this.addOptions.push(mm);
								 						  };
														 					
													}				
												}
										});
    				            }
    					}
    				});
    
    
    
    
    
    
    
    
    
			//获取已审核案例数据
			_this.getCaseDatas=function(pageNo){
				var adminType=_this.admin.userLevel
					var proadd='',
						townadd='';				
				if(adminType=='groupUser'){
					proadd=_this.addValue
					townadd=undefined
				}else{
					proadd=undefined
					townadd=_this.addValue
				}
				if(_this.addValue=='全国'||_this.addValue=='全省'){
					_this.addValue=undefined
				}
				if(_this.classValue=='所有分类'){
					_this.classValue=undefined
				}		
				_this.curPageNo=pageNo;
				var _data={
					category: "Samples",
					conditions: {
						checkState: adminType=='provinceUser'?{$in: [4,5,6,7]}:{$in: [6,7]},
						'userInfo.proAddress':proadd==undefined?undefined:{$regex:_this.addValue},
						'userInfo.townAddress':townadd==undefined?undefined:{$regex:_this.addValue},
						caseType:_this.classValue,
						caseTitle:_this.searchTitle==undefined?undefined:{$regex:_this.searchTitle}
					},
					pageNumber: pageNo || _this.curPageNo,
					pageSize: 6,
					sort: {createTime: -1}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.dataList=rs.list;
						_this.totalPage=rs.count;
						
					},
					error:function(jqXhr,statusCode,statusText){
//						alert(statusText)
					}
				});	
			};
				//获取用户信息
			getUserInfo();
			function getUserInfo(){
					$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.admin=res.content;
						if(_this.admin.userLevel=='groupUser'){
							_this.liHeight='300px'
						}else{
							_this.liHeight='240px'
						}
						_this.getCaseDatas(0);
						
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
			};
    },
    components:{
    	pop
    }
  }
</script>
<style scoped>
 .ui-case-list  .ui-bottom{
    overflow: hidden;
  }
 .c-top{
   margin-bottom: 40px;
   overflow: hidden;
 }

  .ui-case-list .ui-bottom button{
    width: 50%;
    height: 60px;
    line-height: 60px;
    float: left;
    border: none;
    background: #ffffff;
    color: #333;
    font-size: 16px;
    outline: none;
    cursor: pointer;
  }
  .ui-case-list .ui-name span{
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 30px;
    margin-right: 5px;
  }
  .ui-case-list .pre-block{
    background-color: rgba(0,0,0,.3);
  }
 .ui-case-list .ui-name{
   padding-left: 10px;
 }
 .ui-top{
 	cursor: pointer;
 }

</style>



