<template>
	<div id="app">
	  <header class="header-top">
	  	<div class="welcome">渠道智赢</div>
	  	<img class="logo" src="../resource/img/logo.png" />
	  	<h2>后台管理系统</h2>
	  	<div id="login">
	  				欢迎<span class="name">呵呵哒</span>登录<a href="http://qdzy.internal-i-focusing.com/user/signOut">&nbsp;&nbsp;退出</a>
	  		</div>	
	  </header>
	  <div class="view-menu">
	   <ul>
	  	  	<li class="one-list" id="list1">
	  	  		<div class="one-list-tree">
	  	  			<span><i class="iconfont">&#xe601;</i>案例管理</span>
	  	  			<img class="img1" src="./common/img/arrows.png" />
	  	  		</div>
		  	  	    <ul>
		  	  	    	<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list"><router-link to="/notAuditCase"><i class="iconfont">&#xe602;</i><span @click="notAudit">未审核案例</span></router-link></li>
		  	  	    	<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list"><router-link to="/auditCase"><i class="iconfont">&#xe602;</i>已审核案例</router-link></li>
		  	  	    	<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list"><router-link to="/rejectCase"><i class="iconfont">&#xe602;</i>案例驳回</router-link></li>
		  	  	    	<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list"><router-link to="/statistics"><i class="iconfont">&#xe602;</i>案例统计</router-link></li>
		  	  	    	<li v-show="userType=='personalUser'" class="two-list"><router-link to="/MyCase"><i class="iconfont">&#xe602;</i>我的案例</router-link></li>
		  	  	    	<li v-show="userType=='personalUser'" class="two-list"><router-link to="/collectCase"><i class="iconfont">&#xe602;</i>我的收藏</router-link></li>
		  	  	    </ul>
	  	  	</li>
	  	  	<li class="one-list" id="list2">
	  	  		<div class="one-list-tree">
	  	  			<span><i class="iconfont">&#xe609;</i>用户管理</span>
	  	  			<img class="img1" src="./common/img/arrows.png" />
	  	  		</div>
		  	  		<ul>
		  	  			<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list two-list1">
		  	  				<div class="two-list-tree">
		  	  					<span><i class="iconfont">&#xe607;</i>帐号管理</span>
		  	  				</div>
		  	  				<ul>
		  	  					<li class="three-list"><router-link to="/accountInfo"><i class="iconfont">&#xe60a;</i>帐号信息</router-link></li>
		  	  					<li  class="three-list"><router-link to="/authority"><i class="iconfont">&#xe604;</i>权限管理</router-link></li>
		  	  				</ul>
		  	  			</li>
		  	  			<li v-show="userType=='provinceUser'||userType=='groupUser'||userType=='superAdmin'" class="two-list"><router-link to="/pstaTistics"><i class="iconfont">&#xe603;</i>积分管理</router-link></li>
		  	  			<li v-show="userType=='personalUser'" class="two-list"><router-link to="/point"><i class="iconfont">&#xe603;</i>个人积分管理</router-link></li>
		  	  			<li v-show="userType=='personalUser'" class="two-list"><router-link to="/accountInfo"><i class="iconfont">&#xe603;</i>帐号信息</router-link></li>
		  	  		</ul>
	  	  	</li>
	  	 </ul>
	  </div>
	  <div id="rightBox">
	      <router-view></router-view>
	  </div>
	</div>
</template>
<script>
import $ from 'jquery';
   $(function(){
   	  var usert = '';
   	$.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					   async:false, 
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){  					  
    					  		
    					  	if(!ress.error){   					  		
    					  		usert = ress.content.userLevel;
    					  		
    					  		$('.name').text(ress.content.name)
    					  	}else{
    					  		window.location.href='http://qdzy.internal-i-focusing.com/'
    					  	}   					  	  			   
    					  },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    			});	
   	        if(usert!='personalUser'){
   	        	$('#list1').css('height','405px');
   	        $('.view-menu').css('height',$(window).height()-80);
//  	  $('#rightBox').css('height',$(window).height()-80);
    	  window.onresize = function(){
    	  	$('.view-menu').css('height',$(window).height()-80);
//  	    $('#rightBox').css('height',$(window).height()-80);
    };  	  
    	 $('.one-list-tree').click(function(){   	 	
    	 	if($(this).parent().height()>85){
    	 		$(this).children('img').removeClass('rota');
    	 		$(this).children('img').addClass('rota1');
    	 	    $(this).parent().animate({height:'80px'});
    	 	}else{
    	 		$('.one-list').height('80px');
    	 		$('.img1').removeClass('rota');
    	 		$(this).children('img').removeClass('rota1');
    	 		$(this).children('img').addClass('rota');
    	 	   $(this).parent().animate({height:'405px'});	
    	 	}  	 	   
    	 });
    	 $('.two-list-tree').click(function(){
    	 	   if($(this).parent().height()>85){  	 	
    	 	    $(this).parent().animate({height:'80px'});
    	 	}else{
    	 	   $(this).parent().animate({height:'240px'});	 	   
    	 	}
    	 })
   	        }else if(usert=='personalUser'){
   	        $('#list1').css('height','245px');
   	        $('.view-menu').css('height',$(window).height()-80);
//  	  $('#rightBox').css('height',$(window).height()-80);
    	  window.onresize = function(){
    	  	$('.view-menu').css('height',$(window).height()-80);
//  	    $('#rightBox').css('height',$(window).height()-80);
    };  	  
    	 $('.one-list-tree').click(function(){   	 	
    	 	if($(this).parent().height()>85){
    	 		$(this).children('img').removeClass('rota');
    	 		$(this).children('img').addClass('rota1');
    	 	    $(this).parent().animate({height:'80px'});
    	 	}else{
    	 		$('.one-list').height('80px');
    	 		$('.img1').removeClass('rota');
    	 		$(this).children('img').removeClass('rota1');
    	 		$(this).children('img').addClass('rota');
    	 		if($(this).parent().attr('id')=='list2'){
    	 			$(this).parent().animate({height:'325px'});	
    	 		}else{
    	 			$(this).parent().animate({height:'245px'});	
    	 		}
    	 	   
    	 	}  	 	   
    	 });
    	 $('.two-list-tree').click(function(){
    	 	   if($(this).parent().height()>85){  	 	
    	 	    $(this).parent().animate({height:'80px'});
    	 	}else{
    	 	   $(this).parent().animate({height:'160px'});	 	   
    	 	}
    	 })
   	        }
 	  
    });

export default{
	created:function(){
		   var _this = this; 		
 	            $.ajax({
    					  url:"http://qdzy.internal-i-focusing.com/user/getCurrentUser",
    					  dataType:'json',
    					  method:'post',
    					  xhrFields:{withCredentials: true},
    					  data:JSON.stringify({}),
    					  success:function(ress){
    					  	if(!ress.error){
    					  		_this.userType = ress.content.userLevel;
    					  		_this._userId = ress.content._id;
    					  if(location.href.split('#')[1]=='/'){
    					  	if(ress.content.userLevel!='personalUser'){   					
    					  			_this.$router.replace({path:'/notAuditCase'})    					  		   					  		
    					  	}else{
    					  		 _this.$router.replace({path:'/MyCase'})	
    					  	} 
    					   }   					  	   					  		
    					  }  					  	  			   
    					 },
    					  error:function(error){
    					  	  console.log(error)
    					  }
    			});	
	},
	data(){
		return{
			userType:'',
			_userId:''
		}
	},
	methods:{
		notAudit:function(){
				$('#ui-contentt').show();
		}
	}
}
</script>
<style>
	*{
		margin: 0;padding: 0;
	}
@font-face {font-family: 'iconfont';
    src: url('./common/fontsize/iconfont.eot'); /* IE9*/
    src: url('./common/fontsize/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('./common/fontsize/iconfont.woff') format('woff'), /* chrome、firefox */
    url('./common/fontsize/iconfont.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url('./common/fontsize/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}
a{
	text-decoration: none;
}
.router-link-active{
	color: #fbf81c!important ;
}
.router-link-active i{
	color: #fbf81c!important;
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;}
.header-top{
		width: 100%;
		min-width: 1080px;
		height: 74px;
		background-image:url(common/img/header.png) ;
		overflow: hidden;		
		position: fixed;
		z-index: 10;
	}
.header-top h2{
	display: inline;
	color: #333333;
	font-size: 28px;
	margin-left: 15px;
}	
.logo{
	height: 60px;
	width: 60px;
	vertical-align: bottom;
	display: inline;
	margin-bottom: 14px;
	margin-left: 40px;
}
.one-list{
	height: 80px;
	overflow: hidden;
	line-height: 80px;
}
.two-list{
	height: 80px;
	background: #2984c3;
	border-bottom: 1px solid #3791cf;
	color: white;
}
.two-list a{
	color: white;
	font-size: 20px;
}
.three-list{
	height: 80px;
	background: #1b6ba2;
	border-bottom: 1px solid #3791cf;
}

.two-list1{
	overflow: hidden;
}
.one-list-tree{
	width: 210px;
	height: 80px;
	border-bottom: 1px solid #2984c3;
	background: #3497db;
	cursor: pointer;
	font-size: 22px!important;
	color: white;
}
.two-list-tree{
	width: 210px;
	height: 80px;
	cursor: pointer;
	font-size: 20px;
}
.three-list a{
	font-size: 18px;
}
.view-menu{
	width: 210px;
	position: absolute;
	min-height: 830px;
	text-indent: 1em;
	background: #41a1e3;
	position: fixed;
	top: 74px;
}
.rota{
transform:rotate(90deg);
-ms-transform:rotate(90deg); 	/* IE 9 */
-moz-transform:rotate(90deg); 	/* Firefox */
-webkit-transform:rotate(90deg); /* Safari 和 Chrome */
-o-transform:rotate(90deg); 	/* Opera */
}
.rota1{
transform:rotate(0deg);
-ms-transform:rotate(0deg); 	/* IE 9 */
-moz-transform:rotate(0deg); 	/* Firefox */
-webkit-transform:rotate(0deg); /* Safari 和 Chrome */
-o-transform:rotate(0deg);
}
#rightBox{
margin-left: 210px;
background:#f3f7f8;
min-height: 830px;
min-width: 1024px;
overflow-y: auto;
position: relative;
top: 74px;
}
.welcome{
	height: 80px;
	width: 210px;
	display: inline-block;
	text-align: center;
	line-height: 80px;
	color: #FFFFFF;
	font-size: 24px;
	background-image: url(common/img/welcome.png);
}
.img1{
	height: 18px;
	margin-left: 30px;
}
.iconfont{
	font-size: 22px;
	color: white;
	margin-right: 5px;
}
#login{
	float: right;
	display: inline-block;
	line-height: 80px;
	margin-right:50px;
	color: #666666;
}
#login a{
	color: #45A8DC;
	text-decoration: underline;
	margin-left: 10px;
	text-align: center;
}

</style>



