<template>
	<div class="my-echarts">
		<div class="e-nav-top">
			
			<div class="fr nav-right">
				 <div class="block fl">
					    <el-date-picker
					      v-model="timedata"
					      type="daterange"
					      align="right"
					      placeholder="选择日期范围"
					      @change="timeChange"
					      :picker-options="pickerOptions2">
					    </el-date-picker>
				</div>
				 <div class="nav-btn bg-blue" v-on:click='timeChange()'>刷新数据</div><div class="nav-btn bg-grav"><router-link to="/pstaTistics">返回</router-link></div>
				 
			</div>
			
		</div>
		<div class="e-content">
			<button class="btn-export">导出</button>
			<div id="proEchart" style="width: 100%;height:400px;"></div>
			<button class="btn-export">导出</button>
			<div id="preEchart" style="width: 100%;height:400px;"></div>
		</div>
	</div>
	
</template>
<script type="text/javascript">
	import Vue from 'vue'
	import $ from 'jquery'
	import echarts from 'echarts'
	
	export default {
      name: 'MyCase',
      data () {
        return {
        	domain:'http://qdzy.internal-i-focusing.com',
	        pickerOptions2: {
	          shortcuts: [{
	            text: '最近一个月',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近三个月',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近半年',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
	              picker.$emit('pick', [start, end]);
	            }
	          }, {
	            text: '最近一年',
	            onClick(picker) {
	              const end = new Date();
	              const start = new Date();
	              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
	              picker.$emit('pick', [start, end]);
	            }
	          }]
	        },
	        timedata: [new Date().getTime() - 3600 * 1000 * 24 * 30,new Date().getTime()],
	        		//刷新数据
			reloadData:function(){
				console.log('重新加载数据！')
			},
			getPro:'',//各省积分
			getPer:'',//个人积分
			initProEchart:'',
			initPreEchart:'',
			userType:''
        }
      },
      mounted:function(){    
      	var _this=this;   
      	_this.userType=_this.$parent.userType
      	console.log('======='+_this.userType)

		
		//各省积分图echarts
		_this.initProEchart=function(xdata,ydata){			
	      	var proChart = echarts.init(document.getElementById('proEchart'));
	      	proChart.setOption({
		    title: { 
		    	text: _this.userType=='provinceUser'?'地市积分TOP10':'各省积分TOP10' 
		    },
		    tooltip: {},
		    xAxis: {
		        data: xdata
		    },
		    yAxis: {},
		    series: [{
		        name: '积分',
		        type: 'bar',
		        data: ydata,
		        barWidth:40,
		        
		        itemStyle:{
		        	normal:{
		        		color:'#60a7e7',
		        		label: {
                            show: true,
                            position: 'top'
                        }
		        	},
		        	
		        }
		    }]
			})
		};
		
		//个人积分图		
		_this.initPreEchart=function(xdata,ydata){
			var preEchart = echarts.init(document.getElementById('preEchart'));
		      	preEchart.setOption({
			    title: { 
			    	text: '个人积分TOP10' 
			    },
			    tooltip: {},
			    xAxis: {
			        data: xdata
			    },
			    yAxis: {},
			    series: [{
			        name: '积分',
			        type: 'bar',
			        data: ydata,
			        barWidth:40,
			        
			        itemStyle:{
			        	normal:{
			        		color:'#60a7e7',
			        		label: {
	                            show: true,
	                            position: 'top'
	                        }
			        	},			        	
			        }
			    }]
				})
		};
		_this.getPro=function(timeData){
			var _url=_this.domain+'/cusWifs/statistics/getProvinceScoreReportByProvince';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				console.log(res.statistics)
      				var xdata=[];
      				var ydata=[];
      				for(var x in res.statistics){
      					xdata.push(res.statistics[x].province);      				
      					ydata.push(res.statistics[x].sumScore);
      				}
      				_this.initProEchart(xdata,ydata)     				
      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});  
		};
		_this.getPer=function(timeData){
			var _url=_this.domain+'/cusWifs/statistics/getUserScoreReportbyCreateTime';
      		$.ajax({
      			type:"post",
      			url:_url,
      			async:true,
      			dataType:'json',
      			data:JSON.stringify(timeData),
      			success:function(res){
      				var xdata=[];
      				var ydata=[];
      				for(var x in res.statistics){
      					xdata.push(res.statistics[x].username);      				
      					ydata.push(res.statistics[x].sumScore);
      				}
      				_this.initPreEchart(xdata,ydata) 

      			},
      			error:function(jqXhr,statusCode,statusText){
//    				alert(statusText)
      			}
      		});  
		};
		
		 //第一次加载数据
		 function initDatas(){		 	
		 	var start=new Date().getTime() - 3600 * 1000 * 24 * 7;
		 	var end=new Date().getTime();	
      		_this.timeData=[start,end];      		
      		var time={
      			startTime:start,
      			endTime:end
      		}; 
      		$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.userType=res.content.userLevel;
						_this.getPro(time);   
      					_this.getPer(time); 						
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
      		
		 	
		 };
		 initDatas();
      },
      
       methods:{
      	timeChange:function(){
      		var _this=this;
      		var timeData={
      			startTime:_this.timedata[0].getTime(),
      			endTime:_this.timedata[1].getTime()
      		};
      		_this.getPro(timeData);   
      		_this.getPer(timeData);      		
      	},  
      },
     }
</script>
<style scoped="">
	.my-echarts{
		padding: 20px;
		
	}
	.e-nav-top{
		overflow: hidden;
		padding:10px;
	}
	.col-grav{
		color: #CCCCCC;
	}
	.nav-btn{
		display: inline-block;
		float: left;
		width: 120px;
		height: 42px;
		line-height: 42px;
		text-align: center;
		border: none;
		margin-left:20px;
		outline: none;
		cursor: pointer;
		font-size: 16px;	
		color: #FFFFFF;	
		border-radius: 5px;
	}
	.nav-btn a{
		color: #FFFFFF!important;	
		text-decoration: none;
	}
	.bg-blue{
		background: #45A8DC;
		
	}
	.bg-grav{
		background: #999999;
	}
	.e-content{
		margin-top: 10px;
		position: relative;
		
	}
	.btn-export{
		width: 100px;
		height: 35px;
		border: 1px solid #E5E5E5;
		border-radius: 5px;
		color: #999999;
		font-size: 16px;
		background: #FFFFFF;
		outline: none;
		position: absolute;
		right: 20px;
		cursor: pointer;		
	}
	.el-date-editor--daterange.el-input{
		width: 270px;
	}
</style>


